@import 'variables';

.account {
  &-page {
    background: $violet1;
    min-height: calc(100vh - 70px);
    @include w($md) {
      min-height: auto;
    }
    .container-md1 {
      @include w(1400, $md) {
        margin: 0 100px 0 150px;
      }
    }
    .back-link {
      @include w($md) {
        position: relative;
        top: auto;
        left: auto;
        margin-bottom: 0;
      }
    }
  }
  &-info {
    &-actions {
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-end;
      position: relative;
      z-index: 2;
      margin-bottom: 30px;
      top: -15px;
      @include w($sm) {
        display: block;
        margin-top: 25px;
        margin-bottom: 44px;
        top: 0;
      }
    }
    &-btn {
      color: $blue;
      font-size: 1em;
      line-height: 1.6;
      font-weight: 600;
      letter-spacing: -0.24px;
      padding: 3px 14px;
      border-radius: 8px;
      // background: rgba($blue, 0.2);
      background-color: #1f3d6c;
      margin-left: 12px;
      @include center;
      svg {
        margin-right: 9px;
      }
      @include offHoverOnDevices {
        transition: 0.3s;
        &:hover {
          // background: rgba($blue, 0.3);
          background-color: #1f507b;
        }
      }
      @include w($sm) {
        margin-left: 0;
        margin-bottom: 12px;
      }
    }
    &-cover {
      height: 122px;
      background: $violet2;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      width: calc(100% - 76px);
      @include img-full(cover);
      @include w($md) {
        width: 100%;
      }
      @include w($sm) {
        height: 196px;
      }
    }
    &-desc {
      position: relative;
      z-index: 2;
      display: flex;
      align-items: flex-end;
    }
    &-avatar {
      @include img-full(cover);
      width: 81px;
      height: 81px;
      background: url('./../images/logo-icon.svg') no-repeat center / cover;
      border-radius: 50%;
      margin-right: 30px;
      @include w($sm) {
        width: 70px;
        height: 70px;
        margin-right: 10px;
      }
    }
    &-name {
      @include text-white15;
    }
    &-date {
      @include text-white15;
      opacity: 0.4;
    }
  }
  &-tabs {
    .title-h3 {
      margin: 30px 0 24px;
    }
    .tournaments-block__tournaments-table {
      margin-top: 0;
    }
    .tabs-menu {
      margin-bottom: 30px;
    }
    &-empty {
      width: 100%;
      border: 1px dashed #453d66;
      border-radius: 8px;
      padding: 44px;
      @include center;
      flex-direction: column;
      @include w($sm) {
        padding: 24px 16px;
      }
      svg {
        color: $blue;
        font-size: 70px;
        @include w($sm) {
          font-size: 40px;
        }
      }
      .title-h3 {
        text-align: center;
      }
      &-link {
        @include text-white15-s;
        color: $violet1;
        background: $blue;
        padding: 10px 15px;
        text-align: center;
        border-radius: 4px;
        @include w($sm) {
          width: 100%;
        }
      }
    }
  }
  &-menu {
    width: 225px;
    margin-top: 60px;
    padding-right: 35px;
    flex-shrink: 0;
    @include w($sm) {
      display: none;
    }
    hr {
      background: rgba(#e1e6f0, 0.2);
      height: 1px;
      border: 0;
      margin: 10px 0;
    }
    &-link {
      display: flex;
      align-items: center;
      margin-top: 10px;
      @include text-white15;
      // @include offHoverOnDevices {
      //   &:hover {
      //     color: $blue;
      //   }
      // }
      svg {
        margin-right: 15px;
      }
      &--current {
        color: $blue;
      }
      &--premium {
        color: $yellow;
      }
    }
  }
  &-main {
    display: flex;
  }
  &-content {
    padding-top: 30px;
    flex-grow: 1;
    @include w($sm) {
      padding-top: 24px;
    }
  }
  &-title-block {
    display: flex;
    justify-content: space-between;
    @include w($md) {
      display: block;
    }
  }
  &-my-bookmarks {
    padding-top: 30px;
    display: grid;
    grid-template-columns: 1fr;
    grid-row-gap: 35px;
    @include w($md) {
      grid-template-columns: repeat(2, 1fr);
      grid-column-gap: 20px;
    }
    @include w($sm) {
      padding-top: 24px;
      grid-template-columns: 1fr;
    }
    .article-preview {
      display: grid;
      grid-template-columns: 337px auto;
      position: relative;
      @include w($md) {
        display: flex;
      }
      &__img {
        margin-right: 43px;
        grid-row: 1/6;
        @include w($md) {
          margin-right: 0;
          margin-top: 10px;
        }
        a {
          padding-top: 66%;
          @include w($md) {
            padding-top: 55.8%;
          }
        }
      }
      &__tags {
        margin-top: 0;
        grid-column: 2/3;
        @include w($md) {
          margin-top: 15px;
        }
      }
      &__caption {
        margin-top: 5px;
        grid-column: 2/3;
      }
      &__text {
        margin-top: 8px;
        grid-column: 2/3;
        opacity: 0.4;
      }
      &__info {
        grid-column: 2/3;
        grid-row: 5;
        margin-top: 10px;
      }
      &__author-name {
        margin-left: 8px;
      }
    }
    .bookmark-remove-btn {
      position: absolute;
      z-index: 1;
      top: 5px;
      right: 0;
      @include w($md) {
        position: static;
        align-self: flex-end;
      }
      svg {
        color: $blue;
        font-size: 14px;
        @include w($md) {
          font-size: 24px;
        }
      }
    }
    &-empty {
      width: 100%;
      border: 1px dashed #453d66;
      border-radius: 8px;
      padding: 44px;
      margin-top: 30px;
      @include center;
      flex-direction: column;
      @include w($md) {
        padding: 32px 38px;
      }
      @include w($sm) {
        padding: 24px 16px;
      }
      svg {
        color: $white;
        font-size: 53px;
        @include w($sm) {
          font-size: 43px;
        }
      }
      .title-h3 {
        text-align: center;
        margin: 30px 0 8px;
        @include w($md) {
          margin: 24px 0 16px;
        }
        @include w($sm) {
          margin: 16px 0;
        }
        & + p {
          @include text-white15;
          text-align: center;
        }
      }
    }
  }
  &-search-form {
    width: 100%;
    max-width: 266px;
    display: flex;
    height: 38px;
    align-items: center;
    padding: 0 16px;
    border-radius: 8px;
    background-color: rgba($blue, 0.2);
    @include w($md) {
      margin-top: 16px;
    }
    @include w($sm) {
      max-width: none;
    }
    button {
      @include center;
      color: $blue;
      margin-right: 16px;
    }
    input {
      @include text-white15;
      color: $blue;
      flex-grow: 1;
    }
  }
  &-latest-news {
    padding-top: 80px;
    @include w($md) {
      padding-top: 70px;
    }
    @include w($sm) {
      padding-top: 40px;
    }
    .latest-news {
      padding-top: 20px;
      @include w($sm) {
        padding-top: 24px;
      }
    }
    .article-preview {
      &__img {
        a {
          padding-top: 66%;
        }
      }
      &__tags {
        margin-top: 24px;
        @include w($md) {
          margin-top: 15px;
        }
      }
      &__caption {
        margin-top: 8px;
        font-size: 1em;
        @include w($md) {
          margin-top: 5px;
        }
      }
    }
  }
  &-go-to-premium {
    width: 100%;
    border: 1px dashed #453d66;
    border-radius: 8px;
    padding: 44px;
    margin-top: 30px;
    @include center;
    flex-direction: column;
    @include w($md) {
      padding: 32px 38px;
    }
    @include w($sm) {
      padding: 24px;
    }
    .title-h3 {
      max-width: 23.2em;
      text-align: center;
      @include w($sm) {
        font-size: 1em;
        line-height: 1.6;
      }
    }
    .button {
      margin-top: 30px;
      padding: 13px 15px;
      color: $violet1;
      @include w($md) {
        margin-top: 24px;
        padding: 8px 20px;
      }
      @include w($sm) {
        margin-top: 20px;
      }
      svg {
        margin-right: 10px;
        font-size: 16px;
      }
    }
  }
  &-settings {
    &-block {
      padding-top: 45px;
      @include w($sm) {
        padding-top: 30px;
      }
      &--file-load {
        .account-settings-subtitle {
          margin-top: 4px;
        }
      }
      &--account-del {
        .button {
          margin-top: 20px;
          height: 40px;
          border-radius: 4px;
          @include w($sm) {
            width: 100%;
          }
        }
      }
    }
    &-title {
      font-size: 1.334em;
      line-height: 1.75;
      color: $white;
      font-weight: 800;
      letter-spacing: 0.35px;
    }
    &-subtitle {
      @include text-white15;
      margin-top: 20px;
      @include w($sm) {
        margin-top: 10px;
      }
    }
    &-file-loading {
      margin-top: 20px;
      width: 100%;
      padding: 44px;
      @include center;
      flex-direction: column;
      border: 1px dashed #453d66;
      border-radius: 8px;
      @include w($sm) {
        padding: 24px 16px;
      }
      p {
        font-size: 0.9333em;
        line-height: 1.52;
        color: $white;
        text-align: center;
      }
      .profile-form-file-loading {
        margin-top: 24px;
      }
    }
    &-document {
      &-block {
        margin-top: 30px;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 30px 20px;
        @include w($md) {
          grid-template-columns: repeat(2, 1fr);
        }
        @include w($sm) {
          grid-template-columns: 1fr;
        }
      }
      &-item {
        display: grid;
        grid-template-columns: auto 1fr;
        grid-column-gap: 20px;
      }
      &-icon {
        width: 64px;
        height: 64px;
        border-radius: 50%;
        border: 1px dashed $white;
        grid-row: 1 / span 2;
        @include center;
      }
      &-label {
        // @include text-white15;
        font-size: 1em;
        line-height: 1.6;
        color: $white;
        letter-spacing: -0.24px;
      }
      &-btn {
        // @include text-white15-s;
        font-size: 1em;
        line-height: 1.6;
        font-weight: 600;
        letter-spacing: -0.24px;
        color: $blue;
      }
    }
  }
}

.overview {
  &-block {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
  &-title {
    @include text-white15;
    color: #746e8c;
    margin-bottom: 16px;
  }
  &-line {
    background: rgba($white, 0.1);
    overflow: hidden;
    border-bottom: 1px solid $border-light;
    padding: 16px 32px;
    display: flex;
    width: 100%;
    align-items: center;
    @include w($md) {
      justify-content: space-between;
    }
    @include w($sm) {
      padding: 16px;
    }
    &-td {
      width: 45%;
      @include w($md) {
        width: 30%;
      }
      @include w($sm) {
        width: auto;
      }
    }
    &:first-child {
      border-top-right-radius: 8px;
      border-top-left-radius: 8px;
    }
    &:last-child {
      border-bottom-right-radius: 8px;
      border-bottom-left-radius: 8px;
      border-bottom: 0;
    }
  }
}

.team {
  &-wrap {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  &-block {
    width: calc(50% - 10px);
    background: rgba($white, 0.1);
    border-radius: 8px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    align-content: flex-start;
    padding: 20px;
    @include w($sm) {
      width: 100%;
      margin-bottom: 30px;
      padding: 24px 16px;
    }
    &--empty {
      padding: 34px;
      @include w(1366) {
        padding: 20px;
      }
      @include w($sm) {
        padding: 24px 16px;
      }
      .team-info {
        margin-bottom: 30px;
        @include w($sm) {
          margin-bottom: 24px;
        }
        &-picture {
          display: none;
        }
        &-type {
          @include minw($md) {
            margin-bottom: 0;
          }
        }
      }
      .team-count {
        font-weight: 400;
        padding-left: 0;
        margin-bottom: 12px;
        @include w($sm) {
          margin-bottom: 8px;
        }
      }
      .team-members {
        padding-left: 0;
        @include w($md) {
          margin-left: auto;
        }
      }
    }
  }
  &-info {
    width: 100%;
    display: flex;
    align-items: center;
    margin-bottom: 6px;
    &-picture {
      @include img-full(cover);
      width: 50px;
      height: 50px;
      border-radius: 50%;
      margin-right: 14px;
      flex-shrink: 0;
      @include w($sm) {
        width: 40px;
        height: 40px;
      }
    }
    &-name {
      @include display-white25-eb;
    }
    &-type {
      @include text-white15-s;
      color: $blue;
      flex-shrink: 0;
      padding: 3px 10px;
      border-radius: 8px;
      background: rgba($blue, 0.2);
      margin-left: auto;
      margin-bottom: auto;
    }
  }
  &-count {
    width: 100%;
    @include text-white15-s;
    padding-left: 64px;
    @include w($sm) {
      padding-left: 54px;
    }
  }
  &-members {
    display: flex;
    align-items: center;
    padding-left: 64px;
    margin-top: 24px;
    @include w($sm) {
      margin-top: 16px;
      padding-left: 54px;
    }
  }
  &-member {
    @include img-full(cover);
    width: 44px;
    height: 44px;
    border-radius: 50%;
    margin-right: 12px;
    color: $blue;
    &--empty {
      border: 1px solid $blue;
      width: 40px;
      height: 40px;
    }
    &:last-child {
      margin-right: 0;
    }
  }
  &-btn {
    @include text-white15-s;
    color: $violet1;
    padding: 10px 15px;
    border-radius: 4px;
    background: $blue;
    margin-top: 28px;
    margin-right: auto;
    text-align: center;
    @include w($md) {
      width: 100%;
    }
  }
}

.match-table {
  &-tr {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: rgba($white, 0.1);
    overflow: hidden;
    border-bottom: 1px solid $border-light;
    padding: 16px 32px;
    @include w($md) {
      flex-wrap: wrap;
    }
    @include w($sm) {
      padding: 14px;
    }
    &:first-child {
      border-top-right-radius: 8px;
      border-top-left-radius: 8px;
    }
    &:last-child {
      border-bottom-right-radius: 8px;
      border-bottom-left-radius: 8px;
      border-bottom: 0;
    }
  }
  &-td {
    @include text-white15-s;
    @include center;
    justify-content: flex-start;
    @include minw($md) {
      &:first-child {
        width: 8%;
      }
      &:nth-child(2) {
        width: 16%;
      }
      &:nth-child(3) {
        width: 14%;
      }
    }
    &:first-child {
      font-weight: 800;
    }
    @include w($md) {
      width: 50%;
      padding: 4px 0;
    }
    @include w($sm) {
      width: 100%;
    }
    &--finish {
      &::before {
        content: '';
        @include center;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background: rgba($white, 0.5);
        margin-right: 8px;
      }
    }
    &--deny {
      &::before {
        content: '';
        @include center;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background: #ff449f;
        margin-right: 8px;
      }
    }
    &--win {
      color: #28ffbf;
    }
    // &--loose {
    // }
    &--fail {
      color: #8f71ff;
      svg {
        margin-left: 8px;
      }
    }
  }
}

.profile-form {
  &-data-wrap {
    padding-top: 30px;
    .button {
      margin-top: 4px;
      @include w($md) {
        grid-row: 3/4;
      }
      @include w($sm) {
        grid-row: auto;
        margin-top: 0;
      }
    }
    .info-text {
      font-size: 1em;
      line-height: 1.6;
      color: $white;
      font-weight: 400;
      letter-spacing: -0.24px;
      grid-column: span 2;
      @include w($md) {
        grid-column: span 1;
        grid-row: 3/4;
        font-size: 0.8em;
        line-height: 1.33;
      }
      @include w($sm) {
        grid-row: auto;
      }
      a {
        font-size: 1em;
        line-height: inherit;
      }
    }
  }
  &-2-col-block {
    max-width: 490px;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px 30px;
    @include w($md) {
      max-width: none;
    }
    @include w($sm) {
      grid-template-columns: 1fr;
    }
  }
  &-label {
    display: inline-block;
    @include text-white15;
    margin-bottom: 8px;
    &-wrap {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      .hov-underline-link {
        font-size: 1em;
        line-height: 1.6;
      }
    }
  }
  &-input {
    display: inline-block;
    width: 100%;
    padding: 5px 12px;
    font-size: 0.9333em;
    line-height: 1.52;
    color: $violet2;
    border: 1px solid $border;
    border-radius: 8px;
    background-color: $white;
    &:focus {
      border-color: $border;
    }
    &::placeholder {
      opacity: 0.2;
    }
    &-wrap {
      position: relative;
      svg {
        color: #130f26;
        pointer-events: none;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 12px;
      }
      .eye {
        width: 24px;
        height: 24px;
        cursor: pointer;
        position: absolute;
        bottom: 4px;
        right: 12px;
        background: url('../images/eye-close1.svg') center/contain no-repeat;
        &.eye-close {
          background: url('../images/eye-open1.svg') center/contain no-repeat;
        }
      }
    }
    &.calendar {
      cursor: pointer;
    }
    &.enter-pass {
      padding-right: 46px;
    }
  }
  &-3-col-block {
    padding-top: 45px;
    max-width: 750px;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px 30px;
    /* make the native arrow invisible and stretch it over the whole field so you can click anywhere in the input field to trigger the native datepicker*/
    // input[type='date']::-webkit-calendar-picker-indicator {
    //   position: absolute;
    //   top: 0;
    //   left: 0;
    //   right: 0;
    //   bottom: 0;
    //   width: auto;
    //   height: auto;
    //   color: transparent;
    //   background: transparent;
    // }
    @include w($md) {
      max-width: none;
      grid-template-columns: repeat(2, 1fr);
    }
    @include w($sm) {
      grid-template-columns: 1fr;
      padding-top: 30px;
    }
  }
  .button {
    height: 40px;
    border-radius: 4px;
  }
  &-file-loading {
    input[type='file'] {
      display: none;
    }
  }
  &-pwd-changing {
    margin-top: 20px;
  }
}

.hov-underline-link {
  @include text-white15;
  color: $blue;
  text-decoration: underline rgba($blue, 0);
  transition: text-decoration-color $anim;
  @include offHoverOnDevices {
    &:hover {
      text-decoration-color: rgba($blue, 1);
    }
  }
}

// datepicker
.ui-datepicker {
  &-title select {
    border: 1px solid rgb(118, 118, 118);
    background-color: $white;
    border-radius: 3px;
  }
  & .ui-datepicker-prev-hover {
    left: 2px;
    top: 2px;
  }
  & .ui-datepicker-next-hover {
    right: 2px;
    top: 2px;
  }

  & .ui-datepicker-title select {
    margin: 1px;
  }
}
.ui-widget-header .ui-icon {
  background-image: url(../images/ui-icons_444444_256x240.png);
}
