.article {
  &-page {
    .section-aside {
      &__items {
        position: sticky;
        top: 70px;
        height: calc(100vh - 70px);
      }
    }
    &-inner {
      .title-h2 {
        margin-top: 8px;
      }
      .title-h3 {
        margin: 45px 0 30px;
      }
      .title-h4 {
        margin: 45px 0 20px;
      }
      & > p {
        @include text-white17;
        margin-bottom: 1em;
        opacity: 0.8;
      }
      .list-item-paragraph {
        padding-left: 30px;
        margin-bottom: 30px;
        position: relative;
        &::before {
          content: '';
          position: absolute;
          top: 11px;
          left: 0;
          width: 6px;
          height: 6px;
          border-radius: 50%;
          background-color: $blue;
        }
      }
      .qualification-reg {
        margin-bottom: 8px;
        a {
          color: $blue;
          border-bottom: 1px solid $blue;
          transition: $anim;
          @include offHoverOnDevices {
            &:hover {
              border-bottom: 1px solid transparent;
            }
          }
        }
      }
      .divider {
        width: 100%;
        margin: 45px 0;
        border-top: 1px solid $border-light;
      }
      .subscribe-banner {
        margin-top: 70px;
        padding: 50px 70px 45px;
        &__top {
          display: flex;
          justify-content: space-between;
        }
        &__desc {
          max-width: 360px;
        }
        &__text {
          margin-top: 20px;
        }
        &__icon {
          opacity: 0.2;
          .icon-maingame {
            font-size: 100px;
          }
        }
        &__form {
          margin-top: 35px;
        }
      }
      .subscribe-form {
        &__email-input {
          max-width: 320px;
        }
      }
    }
  }
  &__tags {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  &__action {
    margin-left: 50px;
  }
  &__info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 70px;
    font-size: 0.67em;
    line-height: 2.4;
    color: $white;
    letter-spacing: 0.12px;
  }
  &__date {
    margin-right: 50px;
  }
  &__reading {
    flex-shrink: 0;
    margin-left: auto;
  }
  &__author {
    display: flex;
    align-items: center;
    margin-right: 50px;
    &-img {
      flex-shrink: 0;
      width: 32px;
      height: 32px;
      border-radius: 50%;
      overflow: hidden;
    }
    &-name {
      margin-left: 12px;
      line-height: 1.6;
      text-decoration: underline rgba($white, 0);
      transition: $anim;
      @include offHoverOnDevices {
        &:hover {
          color: $blue;
          text-decoration-color: rgba($blue, 1);
        }
      }
    }
  }
  &__img {
    margin-top: 35px;
    width: 100%;
    border-radius: 12px;
    overflow: hidden;
    &-wrapper {
      position: relative;
      padding-top: 42.6%;
      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}

// media
@include w($md) {
  .article {
    &-page {
      .section-aside {
        position: static;
        width: auto;
        height: auto;
        z-index: initial;
        padding-top: 0;
        margin-top: 45px;
        &__items {
          height: auto;
          display: grid;
          grid-template-columns: repeat(2, 1fr);
          grid-gap: 20px;
        }
        &__item {
          position: relative;
          padding-top: 100%;
          height: 100%;
          border-bottom: none;
        }
      }
      &-inner {
        .title-h2 {
          margin-top: 12px;
        }
        .title-h3 {
          margin-bottom: 20px;
        }
        .qualification-reg {
          margin-bottom: 12px;
        }
        .divider {
          margin: 30px 0;
        }
        .subscribe-banner {
          padding-left: 32px;
          &__desc {
            max-width: 465px;
          }
        }
      }
    }
    &__info {
      margin-top: 45px;
    }
  }
}

@include w($sm) {
  .article {
    &-page {
      &-inner {
        .title-h3 {
          margin-top: 30px;
        }
        .title-h4 {
          margin: 30px 0 16px;
        }
        .divider {
          margin: 20px 0;
        }
        .subscribe-banner {
          padding: 50px 16px 40px;
          &__icon {
            display: none;
          }
        }
        .subscribe-form {
          &__email-input {
            max-width: none;
          }
        }
      }
    }
    &__info {
      margin-top: 30px;
    }
    &__img {
      margin-top: 20px;
    }
  }
}

@include w(640) {
  .article {
    &-page {
      .section-aside {
        &__items {
          grid-template-columns: 1fr;
          grid-row-gap: 30px;
        }
      }
    }
  }
}
