.page-404 {
  padding: 0;
  .tournaments-submenu-block {
    z-index: 1;
  }
  &__body {
    width: 100%;
    height: calc(100vh - 70px);
    min-height: 560px;
    position: relative;
    &::after {
      content: '';
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background: linear-gradient(
        180deg,
        rgba(71, 60, 49, 0) 79.23%,
        #110833 100%
      );
    }
  }
  .background-img {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    // &::after {
    //   content: '';
    //   width: 100%;
    //   height: 100%;
    //   position: absolute;
    //   top: 0;
    //   left: 0;
    //   background: url('../images/fog.png') no-repeat;
    //   background-size: cover;
    //   background-position: center;
    // }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  .fog-img {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__inner {
    width: 100%;
    height: 100%;
    position: relative;
    display: flex;
    align-items: flex-end;
    justify-content: center;
  }
  &__main {
    display: flex;
  }
  &__desc {
    padding-top: 133px;
    position: relative;
    z-index: 1;
    .title-h3 {
      max-width: 6.2em;
    }
    .button {
      display: inline-flex;
      margin-top: 32px;
    }
  }
  &__img-404 {
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      max-width: 100%;
      width: 100%;
    }
  }
}

// media
@include w($sm) {
  .page-404 {
    &__body {
      height: auto;
    }
    &__main {
      display: block;
      width: 100%;
    }
    &__desc {
      padding-top: 40px;
      padding-left: 15px;
      padding-right: 15px;
      .button {
        margin-top: 16px;
        padding-left: 25px;
        padding-right: 25px;
      }
    }
    &__img-404 {
      width: 64%;
      max-width: 315px;
      margin-top: 40px;
      margin-left: auto;
      margin-right: 13%;
    }
  }
}
