.policy-page {
  .content-text {
    margin: 0 auto;
    ul {
      @include text-white17;
      margin: 1em 0;
    }
    li {
      opacity: 0.8;
      margin-bottom: 10px;
      padding-left: 30px;
      position: relative;
      &:last-child {
        margin-bottom: 0;
      }
      &::before {
        content: '';
        position: absolute;
        top: 10px;
        left: 0;
        width: 6px;
        height: 6px;
        background-color: $blue;
        border-radius: 50%;
      }
    }
  }
  .title-h1 {
    margin-bottom: 44px;
  }
  p {
    @include text-white17;
    margin-bottom: 1em;
    opacity: 0.8;
  }
  .title-h2 {
    margin-bottom: 45px;
  }
  .title-h3 {
    margin: 70px 0 24px;
  }
}

// media
@include w($md) {
  .policy-page {
    .title-h3 {
      margin-top: 40px;
    }
  }
}

@include w($sm) {
  .policy-page {
    .content-text {
      li {
        padding-left: 14px;
      }
    }
    .title-h2 {
      margin-bottom: 16px;
    }

    .title-h3 {
      margin-bottom: 16px;
    }
  }
}
