.tournaments-page {
  .content-text {
    margin: 0;
  }
}

.top-tournaments {
  margin-top: 40px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 30px;
  .tournament-card {
    &__label {
      position: absolute;
      top: 10px;
      left: 10px;
      font-size: 0.8em;
      line-height: 1.33;
      color: $black;
      font-weight: 700;
      padding: 3px 8px;
      border-radius: 4px;
      background-color: $blue;
      text-transform: uppercase;
    }
    &__title {
      display: inline-flex;
      margin-top: 24px;
    }
    &__region {
      margin-top: 20px;
      @include text-white15-eb;
    }
    &__award {
      margin-top: 20px;
      @include text-white15-eb;
      display: flex;
      align-items: center;
      svg {
        margin-right: 8px;
        opacity: 0.5;
      }
    }
  }
}

.tournament-card {
  &__logo {
    display: block;
    width: 100%;
    height: 0;
    position: relative;
    padding-top: 61.7%;
    border-radius: 8px;
    overflow: hidden;
    img {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      object-fit: cover;
    }
  }
  &__region {
    display: flex;
    align-items: center;
    opacity: 0.5;
    svg {
      margin-right: 8px;
    }
  }
}

.tournaments-block {
  margin-top: 70px;
  &__tabs-menu {
    display: flex;
    border-bottom: 1px solid $border-light;
    span {
      @include text-white15-eb;
      padding: 10px 15px;
      cursor: pointer;
      position: relative;
      transition: $anim;
      &:first-child {
        padding-left: 0;
      }
      &.current {
        color: $blue;
        &::after {
          content: '';
          position: absolute;
          left: 0;
          bottom: -1px;
          width: 100%;
          height: 2px;
          background-color: $blue;
        }
      }
    }
  }
  &__filters {
    display: flex;
    padding-top: 30px;
  }
  &__select-block {
    min-width: 195px;
    height: 40px;
    margin-right: 30px;
    position: relative;
    &:last-child {
      margin-right: 0;
    }
  }
  &__tournaments-table {
    width: 100%;
    margin-top: 35px;
    .tournament-card {
      &__logo {
        width: 100%;
      }
      &__region {
        margin-top: 14px;
        font-size: 0.8em;
        line-height: 2;
        color: $white;
        font-weight: 800;
        letter-spacing: -0.24px;
        svg {
          font-size: 16px;
        }
      }
      &__header {
        width: 322px;
        padding: 15px 15px 15px 0;
        &-wrapper {
          display: grid;
          grid-template-columns: 122px 177px;
          grid-column-gap: 8px;
          align-items: center;
        }
      }
      &__column-title {
        display: none;
        font-size: 0.8em;
        line-height: 2;
        font-weight: 800;
        opacity: 0.5;
      }
      &__title {
        @include text-white15-eb;
        font-size: 1em;
        line-height: 1.6;
      }
      &__status {
        @include text-white15-eb;
        &_current {
          color: #28ffbf;
        }
        &_future {
          color: #8f71ff;
        }
        &_completed {
          color: #e1e6f0;
        }
        &_canceled {
          color: #ff1967;
        }
      }
      &__date {
        margin-top: 9px;
        @include text-white15;
        opacity: 0.8;
        white-space: nowrap;
      }
      &__mode {
        @include text-white15-eb;
        color: $blue;
      }
      &__mode-text {
        margin-top: 9px;
        @include text-white15;
        opacity: 0.8;
        white-space: nowrap;
      }
      &__acces-type {
        margin-top: 7px;
        color: $blue;
      }
      &__participants {
        @include text-white15-eb;
      }
      &__award {
        @include text-white15-eb;
      }
    }
  }
  &__tabs {
    .pagination {
      margin-top: 30px;
    }
  }
}

table {
  border-collapse: collapse;
}

.tournaments-table {
  &__header {
    tr {
      border-bottom: 1px solid $border-light;
    }
    th {
      padding: 10px 15px;
      @include text-white15-s;
      &:first-child {
        padding-left: 0;
      }
    }
  }
  &__body {
    tr {
      &:nth-child(1) {
        td {
          padding-top: 30px;
        }
      }
    }
    td {
      padding: 15px;
      vertical-align: top;
    }
  }
}

.select-block {
  &__btn {
    width: 100%;
    height: 100%;
    position: relative;
    display: flex;
    align-items: center;
    @include text-white15-eb;
    padding: 0 25px 0 12px;
    transition: $anim;
    border: 1px solid $border-light;
    border-radius: 4px;
    svg {
      position: absolute;
      top: 50%;
      right: 12px;
      transition: $anim;
      transform: translateY(-50%);
    }
    &.open {
      border: 1px solid $blue;
      svg {
        color: $blue;
        transform: translateY(-50%) rotate(180deg);
      }
    }
    @include offHoverOnDevices {
      &:hover {
        border: 1px solid $blue;
      }
    }
  }
  &__list {
    position: absolute;
    opacity: 0;
    visibility: hidden;
    width: 100%;
    margin-top: 0.5em;
    background-color: $violet2;
    border: 1px solid $border-light;
    border-radius: 4px;
    padding: 0.5em 0;
    z-index: 1;
    transform: translateY(8px);
    transition: $anim ease-in-out;
    &.open {
      opacity: 1;
      visibility: visible;
      transform: translateY(0);
    }
    li {
      display: flex;
      align-items: center;
      @include text-white15-eb;
      padding: 0 12px;
      height: 32px;
      transition: $anim;
      cursor: pointer;
      @include offHoverOnDevices {
        &:hover {
          background-color: $border-light;
        }
      }
      &.active {
        color: $blue;
        background-color: $border-light;
      }
    }
  }
}

.pagination {
  @include center;
  a {
    @include center;
    font-size: 1.14em;
    line-height: 1.53;
    color: $white;
    font-weight: 400;
    letter-spacing: 0.12px;
  }
  &__item {
    min-width: 27px;
    margin: 0 8px;
    border-bottom: 2px solid transparent;
    transition: $anim;
    &.current {
      color: $blue;
      border-bottom: 2px solid $blue;
    }
    @include offHoverOnDevices {
      &:hover {
        color: $blue;
      }
    }
    &_dash {
      height: 28px;
      position: relative;
      &::after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 15px;
        height: 1px;
        background-color: $white;
        transition: $anim;
      }
      @include offHoverOnDevices {
        &:hover {
          &::after {
            background-color: $blue;
          }
        }
      }
    }
  }
  &__prev,
  &__next {
    width: 27px;
    height: 27px;
    position: relative;
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      width: 10px;
      height: 10px;
      border-right: 2px solid $blue;
      border-bottom: 2px solid $blue;
    }
  }
  &__prev {
    margin-right: 11px;
    &::after {
      transform: translate(-50%, -50%) rotate(135deg);
    }
  }
  &__next {
    margin-left: 11px;
    &::after {
      transform: translate(-50%, -50%) rotate(-45deg);
    }
  }
}

.tabs {
  &-menu {
    display: flex;
  }
  &-menu-item {
    cursor: pointer;
  }
  &-content {
    & > li {
      width: 100%;
      opacity: 0;
      top: 0;
      position: absolute;
      z-index: -1;
      height: 0;
      overflow: hidden;
      &.current {
        height: auto;
        overflow: visible;
        width: 100%;
        z-index: 2;
        opacity: 1;
        position: relative;
        transition: 0.3s;
      }
    }
  }
}

.tournaments-seo {
  padding-top: 35px;
  &__text {
    height: 14.5em;
    overflow: hidden;
    transition: 0.5s;
  }
  .title-h3 {
    @include display-white25-eb;
    font-weight: 700;
    margin-top: 45px;
    margin-bottom: 20px;
  }
  p {
    @include text-white17;
    margin-bottom: 1em;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__btn {
    display: flex;
    align-items: center;
    margin-top: 20px;
    @include text-white15-s;
    color: $blue;
    .hide {
      display: none;
    }
    svg {
      margin-left: 8px;
    }
    &.open {
      .show {
        display: none;
      }
      .hide {
        display: block;
      }
      svg {
        transform: rotate(180deg);
      }
    }
  }
}

// media
@include w($md) {
  .top-tournaments {
    grid-column-gap: 16px;
    .tournament-card {
      &__title {
        margin-top: 16px;
      }
      &__region {
        margin-top: 16px;
        font-size: 0.8em;
        line-height: 2;
        svg {
          font-size: 16px;
        }
      }
      &__award {
        margin-top: 7px;
        font-size: 0.8em;
        line-height: 2;
        svg {
          font-size: 14px;
        }
      }
    }
  }
  .tournaments-block {
    margin-top: 20px;
    &__tournaments-table {
      margin-top: 20px;
      .tournament-card {
        &__block {
          display: grid;
          grid-template-columns: 1fr 1fr;
        }
        &__header {
          width: 100%;
          grid-column: 1/3;
          padding: 0;
          &-wrapper {
            grid-template-columns: 1fr;
          }
          &-desc {
            margin-top: 15px;
            padding-bottom: 8px;
            border-bottom: 1px solid $border-light;
          }
        }
        &__column {
          &-title {
            display: block;
            margin-bottom: 8px;
          }
          &_half {
            padding-top: 8px !important;
            padding-bottom: 8px;
            border-bottom: 1px solid $border-light;
            &:nth-child(even) {
              padding-right: 8px;
            }
            &:nth-child(odd) {
              padding-left: 8px;
            }
          }
        }
        &__region {
          margin-top: 8px;
        }
        &__status {
          font-size: 0.8em;
          line-height: 2;
        }
        &__date {
          margin-top: 0;
          font-size: 0.8em;
          line-height: 2;
          white-space: inherit;
        }
        &__mode {
          font-size: 0.8em;
          line-height: 2;
          &-text {
            margin-top: 0;
            font-size: 0.8em;
            line-height: 2;
            white-space: inherit;
          }
        }
        &__participants {
          font-size: 0.8em;
          line-height: 2;
        }
        &__award {
          font-size: 0.8em;
          line-height: 2;
        }
        &__footer {
          width: 100%;
          grid-column: 1/3;
          padding-top: 8px !important;
          padding-bottom: 10px;
        }
      }
    }
  }
  .tournaments-table {
    &__header {
      display: none;
    }
    &__body {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-gap: 20px;

      tr {
        &:nth-child(1) {
          td {
            padding-top: 0;
          }
        }
      }
      td {
        padding: 0;
      }
    }
  }
  .tournaments-seo {
    padding-top: 25px;
  }
}

@include w($sm) {
  .top-tournaments {
    grid-template-columns: 1fr;
    .tournament-card {
      &__award {
        margin-top: 12px;
      }
    }
  }
  .tournaments-block {
    margin-top: 30px;
    &__filters {
      padding-top: 16px;
      display: block;
    }
    &__select-block {
      width: 100%;
      margin-right: 0;
      margin-bottom: 8px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    &__tournaments-table {
      margin-top: 30px;
      .tournament-card {
        &__header {
          &-desc {
            padding-bottom: 15px;
          }
        }
        &__column {
          &_half {
            padding-top: 15px !important;
            padding-bottom: 15px;
          }
        }
      }
    }
  }
  .tournaments-table {
    &__body {
      grid-template-columns: 1fr;
    }
  }
  .tournaments-block__tabs .pagination {
    margin-top: 20px;
  }
  .pagination {
    &__item {
      margin: 0 4px;
    }
  }
  .tournaments-seo {
    padding-top: 6px;
    .title-h3 {
      margin-top: 24px;
      margin-bottom: 16px;
    }
    &__btn {
      margin-top: 16px;
    }
  }
}
