.header {
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  z-index: 11;
  background-color: $violet1;
  padding-left: 3.1vw;
  padding-right: 3.1vw;
  &_fixed {
    position: fixed;
  }
  &__inner {
    display: flex;
    align-items: center;
    padding: 15px 0;
    height: 70px;
  }
  &__logo {
    margin-right: auto;
  }
  .tournaments-submenu {
    display: none;
    margin-top: 47px;
    li {
      margin-bottom: 24px;
    }
    a {
      display: flex;
      align-items: center;
    }
    &__text {
      margin-left: 10px;
      font-size: 0.8em;
      line-height: 1.67;
    }
    .icon {
      font-size: 27px;
      width: 1em;
      opacity: 0.4;
    }
  }
  &__burger {
    display: none;
  }
  &__nav {
    ul {
      display: flex;
    }
    li {
      margin: 0 28px;
    }
    a {
      display: inline-block;
      transition: color $anim;
      @include text-white15-s;
      @include offHoverOnDevices {
        &:hover {
          color: $blue;
        }
      }
    }
  }
  &__search {
    margin-left: 22px;
    height: 42px;
    @include center;
    border-left: 1px solid rgba($white, 0.2);
    border-right: 1px solid rgba($white, 0.2);
    a {
      @include center;
      padding: 0 19px;
      color: $blue;
    }
    &-btn {
      @include center;
      padding: 0 19px;
      color: $blue;
    }
  }
  &__enter {
    display: inline-block;
    margin-left: 40px;
    font-size: 1em;
    line-height: 1.6;
    color: $violet1;
    font-weight: 600;
    letter-spacing: -0.24px;
    span {
      display: inline-block;
      padding: 3px 41px;
      background-color: $blue;
      border-radius: 8px;
    }
    &-icon {
      display: none;
    }
  }
  &.open {
    .header {
      &__menu {
        transform: translateX(0);
        z-index: 1;
      }
      &__nav {
        display: block;
      }
      &__burger {
        position: fixed;
        top: 17px;
        right: 10px;
        z-index: 2;
      }
    }
    .burger {
      &__bar {
        &:nth-child(1) {
          transform: rotate(45deg);
          top: 6px;
        }
        &:nth-child(2) {
          opacity: 0;
        }
        &:nth-child(3) {
          transform: rotate(-45deg);
          top: 6px;
        }
      }
    }
  }
  &__user {
    position: relative;
    margin-left: 40px;
  }
  &__user-profile {
    display: flex;
    align-items: center;
    cursor: pointer;
  }
  &__user-name {
    margin-right: 12px;
    font-size: 1em;
    line-height: 1.6;
    color: $white;
    font-weight: 600;
    letter-spacing: -0.24px;
  }
  &__user-icon {
    @include center;
    flex-shrink: 0;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__user-menu {
    min-width: 220px;
    position: absolute;
    top: calc(100% + 10px);
    right: 0;
    padding: 11px 15px;
    border-radius: 10px;
    background-color: $white;
    opacity: 0;
    visibility: hidden;
    transform: translateY(10px);
    transition-duration: 0.2s;
    transition-property: opacity, visibility, transform;
    transition-timing-function: ease-in-out;
    &.open {
      opacity: 1;
      visibility: visible;
      transform: translateY(0);
    }
  }
}

.user-menu {
  ul {
    display: flex;
    flex-direction: column;
  }
  li {
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__nav-item {
    display: inline-flex;
    align-items: center;
    font-size: 1em;
    line-height: 1.6;
    color: $violet1;
    font-weight: 400;
    letter-spacing: -0.24px;
    position: relative;
    transition: $anim;
    img {
      max-width: 100%;
    }
    svg {
      max-width: 100%;
    }
    &_link {
      @include offHoverOnDevices {
        &:hover {
          color: $blue;
        }
      }
    }
    &_premium {
      color: #ffa959;
      padding-bottom: 10px;
      border-bottom: 1px solid #e1e6f0;
      @include offHoverOnDevices {
        &:hover {
          color: #d27013;
        }
      }
    }
    &-icon {
      width: 15px;
      margin-right: 12px;
      @include center;
    }
  }
}

// media
@include w($md) {
  .header {
    padding: 0 15px;

    &__nav {
      li {
        margin: 0 16px;
      }
    }
    &__search {
      margin-left: 16px;
    }
    &__enter {
      margin-left: 32px;
    }
    &__user {
      margin-left: 32px;
    }
  }
}

@include w($sm) {
  .header {
    padding: 0 $sm-p;
    &__logo {
      img {
        width: 116px;
      }
    }
    &__menu {
      transform: translateX(100%);
      position: fixed;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: $violet1;
      transition: transform 0.5s;
      padding: 85px 10px 55px;
      overflow-y: auto;
      .tournaments-submenu {
        display: block;
      }
    }
    .burger {
      width: 24px;
      height: 24px;
      @include center;
      cursor: pointer;
      &__icon {
        width: 20px;
        height: 14px;
        position: relative;
      }
      &__bar {
        position: absolute;
        left: 0;
        width: 20px;
        height: 2px;
        border-radius: 2px;
        background-color: $blue;
        transition: 0.3s;
        &:nth-child(1) {
          top: 0;
        }
        &:nth-child(2) {
          top: 6px;
        }
        &:nth-child(3) {
          top: 12px;
        }
      }
    }
    &__nav {
      display: none;
      ul {
        display: block;
      }
      li {
        margin: 0 0 16px;
        &:last-child {
          margin-bottom: 0;
        }
      }
      a {
        font-family: 'SF Pro Display';
        font-size: 1.67em;
        line-height: 1.4;
        color: $white;
        font-weight: 800;
        letter-spacing: 0.35px;
      }
    }
    &__search {
      margin-left: 24px;
      height: auto;
      border: none;
      a {
        padding: 0;
      }
      &-btn {
        padding: 0;
      }
    }
    &__enter {
      margin-left: 24px;
      span {
        display: none;
      }
      &-icon {
        display: block;
      }
    }
    &__user {
      margin-left: 24px;
    }
    &__user-name {
      display: none;
    }
    &__user-icon {
      width: 24px;
      height: 24px;
    }
  }
}
