.arena-page {
  padding-top: 0;
  background-color: #271c52;
  .container-md3 {
    @include w(1440) {
      margin: 0 30px;
    }
  }
}

.arena-main {
  position: relative;
  height: calc(100vh - 70px);
  .container-sides-lg {
    display: flex;
    align-items: center;
    height: 100%;
    position: relative;

    // &-md3 {
    //   height: 100%;
    // }
  }
  .container-md3 {
    width: 100%;
  }
  .background-img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    &::after {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      bottom: 0;
      background: #503f97;
      opacity: 0.24;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      mix-blend-mode: overlay;
      opacity: 0.24;
    }
  }
  &__content {
    max-width: 642px;
    margin-bottom: 116px;
    .title-h1 {
      margin-top: 12px;
    }
    p {
      margin-top: 30px;
      @include text-white15;
    }
    .actions-block {
      display: flex;
      margin-top: 45px;
    }
    .button {
      min-width: 200px;
      margin-right: 24px;
      &:last-child {
        margin-right: 0;
      }
    }
  }
  .section-aside {
    position: fixed;
    padding-top: 70px;
  }
}

.recommend-tourneys {
  &-section {
    padding-top: 80px;
    padding-bottom: 80px;
  }
  &__top {
    max-width: 642px;
    p {
      margin-top: 30px;
      @include text-white15;
    }
  }
  &__filters {
    display: flex;
    flex-wrap: wrap;
    margin-top: 45px;
    .button-filter {
      flex-shrink: 0;
      margin-right: 24px;
      margin-bottom: 16px;
      &:last-child {
        margin-right: 0;
      }
    }
  }
  &__tournaments {
    padding-top: 30px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;
  }
}

.recommend-tourney {
  &__icon {
    position: relative;
    padding-top: calc(100% * 0.622);
    border-radius: 12px;
    overflow: hidden;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__desc {
    padding-top: 20px;
  }
  &__game {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
    svg {
      font-size: 18px;
    }
    img {
      width: 18px;
      height: 18px;
      object-fit: cover;
    }
    span {
      margin-left: 8px;
      font-size: 1em;
      line-height: 1.6;
      color: $white;
      font-weight: 800;
      letter-spacing: -0.24px;
    }
  }
  &__caption {
    margin-bottom: 16px;
  }
  &__info {
    opacity: 0.5;
    @include text-white15;
    margin-bottom: 30px;
  }
  &_big {
    margin-top: 45px;
    display: flex;
    grid-column-gap: 30px;
    justify-content: space-between;
    .recommend-tourney {
      &__game {
        margin-bottom: 8px;
      }
      &__icon {
        flex-basis: 532px;
        padding-top: 0;
      }
      &__desc {
        display: block;
        flex-basis: 420px;
        padding-top: 0;
        .button {
          margin-bottom: 15px;
        }
      }
      &__caption {
        margin-bottom: 30px;
      }
      &__info {
        margin-bottom: 35px;
      }
    }
  }
}

.advantages {
  &-section {
    padding-top: 80px;
  }
}

.advantage-item {
  display: grid;
  grid-column-gap: 30px;
  padding: 80px 0;
  border-bottom: 1px solid $border-light;
  &:last-child {
    border-bottom: none;
  }
  &:nth-child(odd) {
    grid-template-columns: minmax(300px, 532px) minmax(300px, 420px);
  }
  &:nth-child(even) {
    grid-template-columns: minmax(300px, 420px) minmax(300px, 532px);
  }
  &__img {
    display: flex;
    align-items: center;
    &_center {
      justify-content: center;
    }
    img {
      max-width: 100%;
    }
  }
  &__desc {
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    &_bottom {
      align-content: flex-end;
    }
  }
  &__caption {
    margin-bottom: 16px;
  }
  &__text {
    margin-bottom: 34px;
    @include text-white15;
  }
}

.ready-to-play {
  @include center;
  margin-top: 30px;
  position: relative;
  min-height: 313px;
  &__bg-img {
    border-radius: 20px;
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(
        0deg,
        rgba(23, 13, 64, 0.6),
        rgba(23, 13, 64, 0.6)
      );
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__main {
    max-width: 552px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__caption {
    margin-bottom: 20px;
    text-align: center;
  }
  &__text {
    margin-bottom: 30px;
    @include text-white15;
    text-align: center;
  }
}

// media
@include w($md) {
  .arena-page {
    .container-md3 {
      margin: 0 auto;
    }
  }
  .arena-main {
    height: auto;
    padding-top: 30px;
    .container-sides-lg {
      display: block;
    }
    .background-img {
      max-height: 780px;
    }
    &__content {
      margin: 0;
    }
    .section-aside {
      position: static;
      width: auto;
      height: auto;
      z-index: initial;
      padding-top: 0;
      margin-top: 70px;
      &__items {
        height: auto;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 30px 20px;
      }
      &__item {
        position: relative;
        padding-top: 108%;
        height: 100%;
        border-bottom: none;
      }
    }
  }
  .recommend-tourneys {
    &-section {
      padding-top: 70px;
      padding-bottom: 45px;
    }
    &__top {
      p {
        margin-top: 40px;
      }
    }
    &__filters {
      flex-wrap: nowrap;
      overflow-x: auto;
      .button-filter {
        margin-bottom: 0;
      }
    }
    &__tournaments {
      grid-template-columns: repeat(2, 1fr);
      grid-gap: 30px 20px;
    }
  }
  .recommend-tourney {
    display: grid;
    grid-auto-flow: row;
    grid-template: auto 1fr/ 1fr;
    &__desc {
      display: grid;
      grid-template: auto 1fr / auto;
      .button {
        justify-self: start;
        align-self: end;
      }
    }
    &__game {
      margin-bottom: 12px;
    }
    &__caption {
      margin-bottom: 12px;
    }
    &_big {
      display: block;
      .recommend-tourney {
        &__icon {
          padding-top: calc(100% * 0.567);
        }
        &__desc {
          padding-top: 24px;
        }
        &__info {
          margin-bottom: 20px;
        }
      }
    }
  }
  .advantages {
    &-section {
      padding-top: 35px;
    }
  }
  .advantage-item {
    padding: 45px 0;
    &__caption,
    &__text {
      margin-bottom: 20px;
    }
  }
  .ready-to-play {
    min-height: 286px;
    margin-top: 0;
    &__text {
      margin-bottom: 24px;
    }
  }
}

@include w(992) {
  .arena-main {
    .section-aside {
      &__items {
        grid-template-columns: repeat(2, 1fr);
      }
    }
  }
  .advantage-item {
    grid-column-gap: 20px;
  }
}

@include w($sm) {
  .arena-main {
    padding-top: 25px;
    &__content {
      max-width: none;
      p {
        margin-top: 16px;
      }
      .actions-block {
        margin-top: 30px;
        flex-wrap: wrap;
      }
      .button {
        width: 100%;
        margin-right: 0;
        margin-bottom: 20px;
        font-size: 1em;
        line-height: 1.6;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
    .section-aside {
      margin-top: 30px;
    }
  }
  .recommend-tourneys {
    &-section {
      padding-top: 45px;
      padding-bottom: 25px;
    }
    &__top {
      p {
        margin-top: 16px;
      }
    }
    &__filters {
      margin-top: 30px;
    }
    &__tournaments {
      grid-template-columns: 1fr;
    }
  }
  .recommend-tourney {
    &__desc {
      display: block;
      .button {
        width: 100%;
      }
    }
    &__caption {
      margin-bottom: 16px;
    }
    &__info {
      margin-bottom: 16px;
    }
    &_big {
      margin-top: 30px;
      .recommend-tourney {
        &__desc {
          padding-top: 20px;
          .button {
            margin-bottom: 0;
          }
        }
        &__caption {
          font-size: 1.34em;
          line-height: 1.5;
          letter-spacing: 0.35px;
          margin-bottom: 16px;
        }
        &__info {
          margin-bottom: 16px;
        }
      }
    }
  }
  .advantages {
    &-section {
      padding-top: 20px;
    }
  }
  .advantage-item {
    padding: 30px 0;
    grid-row-gap: 20px;
    &:nth-child(odd),
    &:nth-child(even) {
      grid-template-columns: 1fr;
    }
    &__img {
      img {
        max-width: none;
        width: 100%;
      }
    }
    &__caption,
    &__text {
      margin-bottom: 16px;
    }
    .button {
      width: 100%;
    }
  }
  .ready-to-play {
    padding: 0 16px;
    &__caption {
      margin-bottom: 16px;
    }
    &__text {
      margin-bottom: 16px;
    }
    .button {
      width: 100%;
    }
  }
}

@include w(640) {
  .arena-main {
    .section-aside {
      &__items {
        grid-template-columns: 1fr;
      }
    }
  }
}
