footer.footer {
  min-height: 750px;
  height: 750px;
  .container-sides-lg {
    height: 100%;
  }
  .footer__content {
    padding-top: 30px;
  }
}

section.footer {
  .footer__content {
    padding-top: 70px;
  }
}

.footer {
  position: relative;
  z-index: 5;
  background-color: #110833;
  &__content {
    position: relative;
    height: 100%;
    padding: 70px 0 0;
    display: flex;
    justify-content: space-around;
  }
  .content-left {
    height: 100%;
    margin-left: 30px;
    padding-right: 30px;
    padding-bottom: 30px;
    flex-basis: 533px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
  &__main {
    .title-h2 {
      max-width: 11.1em;
      margin-top: 10px;
    }
    & > p {
      width: 100%;
      @include text-white15;
      margin-top: 30px;
    }
  }
  &__subscribe {
    margin-top: 7.2vh;
  }
  .content-right {
    height: 100%;
    margin-left: 30px;
    margin-right: 30px;
    padding-top: 20px;
    padding-left: 30px;
    flex-basis: 450px;
    border-left: 1px solid rgba($white, 0.1);
  }
  &__bottom {
    margin-top: 10.8vh;
    p {
      font-size: 0.8em;
      line-height: 1;
      color: $white;
      opacity: 0.5;
      margin-bottom: 1em;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

.subscribe {
  p {
    @include text-white15-eb;
  }
}

.subscribe-form {
  width: 100%;
  max-width: 320px;
  padding-top: 20px;
  label {
    display: inline-block;
    @include text-white15;
  }
  &__email {
    label {
      margin-bottom: 8px;
    }
    input {
      width: 100%;
      padding: 5px 12px;
      border-radius: 8px;
      background-color: $white;
      border: 1px solid $border;
      @include text-white15;
      color: $violet2;
      &::placeholder {
        opacity: 0.2;
      }
    }
  }
  &__agree {
    display: flex;
    margin-top: 24px;
    input[type='checkbox'] {
      &:checked + label::before {
        background: url('../images/check-white.svg') center no-repeat, $blue;
      }
    }
    label {
      display: flex;
      align-items: center;
      font-size: 14px;
      cursor: pointer;
      &::before {
        display: block;
        content: '';
        width: 16px;
        height: 16px;
        border-radius: 2px;
        border: 1px solid $border;
        margin-right: 8px;
      }
    }
  }
  &__submit {
    width: 100%;
    cursor: pointer;
    text-align: center;
    margin-top: 24px;
    padding: 5px 16px;
    background-color: $blue;
    border-radius: 8px;
    @include text-white15;
    letter-spacing: normal;
    color: $black;
  }
}

.social-list {
  li {
    padding: 20px 0;
    border-bottom: 1px solid rgba($white, 0.1);
    &:last-child {
      border-bottom: none;
    }
  }
  &__link {
    display: inline-flex;
    align-items: flex-start;
    @include text-white15;
  }
  &__icon {
    width: 20px;
    height: 20px;
    flex-shrink: 0;
    margin-top: 2px;
    @include center;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__desc {
    margin-left: 15px;
  }
  &__title {
    font-weight: 800;
  }
}

// media
@include minw(1441) {
  section.footer {
    .content-left {
      flex-wrap: wrap;
      margin: 0;
      padding-left: 30px;
      flex-basis: 50%;
      flex-direction: row;
      justify-content: flex-start;
    }
    .content-right {
      padding-top: 0;
      margin: 0;
      flex-basis: 50%;
    }
    .social-list {
      max-width: 420px;
      margin-top: 17.7vh;
      li {
        &:first-child {
          padding-top: 0;
        }
      }
    }
    .footer {
      &__content {
        justify-content: initial;
      }
      &__main {
        margin-top: 17.7vh;
        max-width: 500px;
      }
      &__bottom {
        width: 100%;
        align-self: flex-end;
        margin-top: 0;
      }
    }
  }
}

@include w($md) {
  .footer {
    .section-aside {
      display: none;
    }
    .content-left {
      margin-left: 0;
      flex-basis: 490px;
      padding-right: 0;
    }
    &__main {
      padding-right: 20px;
      .title-h2 {
        font-size: 2.4em;
        line-height: 1.4;
        margin-top: 10px;
      }
    }
    &__subscribe {
      margin-top: 60px;
    }
    &__bottom {
      margin-top: 42px;
    }
    .content-right {
      padding-top: 12px;
      margin-left: 0;
      margin-right: 0;
    }
  }

  footer.footer {
    min-height: auto;
    height: auto;
    .footer__content {
      justify-content: center;
    }
  }
}

@include w($sm) {
  .footer {
    &__content {
      flex-wrap: wrap;
      padding-bottom: 40px;
    }
    .content-left {
      flex-basis: 100%;
      padding-bottom: 12px;
    }
    &__main {
      padding-right: 0;
      .title-h2 {
        font-size: 1.67em;
        letter-spacing: 0.35px;
      }
      & > p {
        margin-top: 16px;
      }
    }
    &__subscribe {
      margin-top: 32px;
    }
    &__bottom {
      margin-top: 16px;
    }
    .content-right {
      flex-basis: 100%;
      padding-left: 0;
      border-left: none;
    }
  }
  .subscribe-form {
    max-width: none;
    &__agree {
      label {
        letter-spacing: normal;
        font-size: 12px;
      }
    }
  }
  .social-list {
    li {
      padding: 8px 0 16px;
    }
    &__desc {
      margin-left: 8px;
    }
  }
}
