@import 'variables';

html {
  font-size: 15px;
}

body {
  font-family: 'SF Pro Text', $ff;
  font-size: 15px;
  color: $white;
  background-color: $background;
}

main {
  position: relative;
  padding: 45px 0 80px;
}

.full-height {
  height: 100%;
}

.container {
  // max-width: 1920px;
  width: 100%;
  margin: 0 auto;
  &-sm {
    max-width: 758px;
    margin: 0 auto;
  }
  &-md1 {
    max-width: 1165px;
    margin: 0 auto;
  }
  &-md2 {
    max-width: 1095px;
    margin: 0 auto;
    @include w(1400, $md) {
      margin: 0 100px 0 150px;
    }
  }
  &-md3 {
    max-width: 982px;
    margin: 0 auto;
  }
  &-sides-lg {
    width: 100%;
    padding-left: 142px;
    padding-right: 255px;
  }
}

.content-text {
  max-width: 58em;
  margin: 0 auto;
}

// === headers ===
.title {
  width: 100%;
  &-h1 {
    @include display-white60-eb;
  }
  &-h2 {
    @include display-white40-eb;
  }
  &-h3 {
    @include display-white25-eb;
  }
  &-h4 {
    font-size: 1em;
    line-height: 1.6;
    color: $white;
    font-weight: 800;
    letter-spacing: -0.24px;
  }
}
// h1 {
//   @include display-white40-b;
// }
// h2 {
//   @include display-white25-b;
// }
// === /headers ===

// === forms ===
input,
textarea {
  @include fixInputIOS;
}
// input::-webkit-outer-spin-button,
// input::-webkit-inner-spin-button {
//   -webkit-appearance: none;
//   margin: 0;
// }
input[type='number'] {
  -moz-appearance: textfield;
}
// input {
//   &::-webkit-calendar-picker-indicator {
//     display: none !important;
//   }
// }
// === /forms ===

// === pages style ===
.pages-page {
  min-height: 50vh;
  padding-top: 50px;
  @include fluid-type(1366px, 1440px, 16px, 20px);

  .container {
    display: flex;
    align-items: center;
    flex-direction: column;
  }

  h1 {
    @include fluid-type(768px, 1600px, 24px, 32px);
    text-transform: uppercase;
    padding: 20px 0 5px;
  }

  h3 {
    @include fluid-type(768px, 1600px, 22px, 24px);
    text-transform: uppercase;
    padding-bottom: 10px;
  }

  a {
    @include center;
    margin: 15px 0;

    &:hover span {
      border-bottom-color: transparent;
    }

    &.done svg {
      color: green;
    }

    &.undone svg {
      color: red;
    }
  }

  span {
    border-bottom: 1px solid;
    margin-right: 10px;
    transition: 0.3s;
  }

  svg {
    font-size: 30px;
    color: #fff;
  }
}
// === /pages style ===

.social {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 6;
  width: 142px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  &__links {
    li {
      margin-bottom: 2em;
      &:last-child {
        margin-bottom: 0;
      }
    }
    a {
      width: 22px;
      height: 22px;
      @include center;
      @include offHoverOnDevices {
        &:hover {
          svg {
            opacity: 1;
            color: $blue;
          }
        }
      }
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
      svg {
        transition: $anim;
        opacity: 0.4;
      }
    }
  }
}

.page-numbering {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  z-index: 1;
  // new
  bottom: 0;
  width: auto;
  z-index: 10;
  position: fixed;
  .top-line {
    position: absolute;
    top: 13.5%;
    left: 0;
    display: flex;
    .number-line {
      width: 44px;
      height: 1px;
      background-color: $white;
      align-self: flex-end;
      margin-bottom: 5px;
    }
    .number {
      line-height: 1;
      font-size: 1.67em;
      color: #fff;
      margin-left: 16px;
      letter-spacing: 0.35px;
    }
  }
  .bottom-line {
    position: absolute;
    left: 0;
    bottom: 6.2%;
    display: flex;
    .number-line {
      width: 44px;
      height: 1px;
      background-color: #fff;
      align-self: flex-end;
      margin-bottom: 5px;
      opacity: 0.5;
    }
    .number {
      margin-left: 16px;
      font-size: 1em;
      line-height: 1.33;
      color: white;
      letter-spacing: -0.24px;
      opacity: 0.5;
    }
  }
}

.homepage {
  section {
    height: 100%;
  }
}

.hashtags {
  a {
    margin-right: 8px;
    @include text-blue10;
    text-transform: uppercase;
    text-decoration: underline rgba($blue, 0);
    transition: text-decoration-color $anim;
    @include offHoverOnDevices {
      &:hover {
        text-decoration-color: rgba($blue, 1);
      }
    }
  }
}

// pushing bookmark button
.bookmark {
  transition: $anim;
  cursor: pointer;
  &:active {
    transform: scale(0.9);
  }
  .icon-mark {
    fill: transparent;
    stroke: #16d5e1;
  }
  &.add {
    .icon-mark {
      fill: $blue;
    }
  }
}
// /pushing bookmark button

.tag {
  @include text-blue12-b;
  text-transform: uppercase;
}

.page-wrapper {
  min-height: 100%;
}

.tournaments-submenu-block {
  position: absolute;
  top: 0;
  right: 0;
  width: 76px;
  height: 100%;
  background-color: rgba($white, 0.05);

  .sticky-block {
    position: sticky;
    top: 70px;
    height: calc(100vh - 70px);
    display: flex;
    flex-direction: column;
  }
  .caption {
    width: 100%;
    height: 76px;
    border-bottom: 3px solid $blue;
    a {
      width: 100%;
      height: 100%;
      @include center;
      font-size: 0.67em;
      line-height: 1.5;
      color: $white;
      font-weight: 800;
      letter-spacing: 0.12px;
      text-align: center;
      text-transform: uppercase;
    }
  }
  .games-list {
    a {
      @include center;
      width: 100%;
      height: 76px;
      border-bottom: 1px solid rgba($white, 0.1);
      @include offHoverOnDevices {
        &:hover .icon {
          opacity: 1;
          color: $blue;
        }
      }
      &.current {
        border-bottom: 3px solid $blue;
        .icon {
          color: $blue;
          opacity: 1;
        }
      }
    }
    .icon {
      opacity: 0.4;
      transition: $anim;
    }
  }
  .tournaments-submenu {
    margin-top: auto;
    li {
      &:nth-child(n + 2) {
        a {
          @include offHoverOnDevices {
            &:hover .icon {
              opacity: 1;
              color: $blue;
            }
          }
        }
      }
      &:nth-child(1) {
        a {
          border-top: 1px solid rgba($white, 0.1);
          @include offHoverOnDevices {
            &:hover .icon {
              opacity: 1;
              color: $blue;
              color: #ffa959;
            }
          }
        }
      }
    }
    a {
      @include center;
      width: 100%;
      height: 76px;
      border-bottom: 1px solid rgba($white, 0.1);
    }
    .icon {
      font-size: 27px;
      width: 1em;
      opacity: 0.4;
      transition: $anim;
    }
  }
}
.tournaments-submenu {
  a {
    &.premium.active {
      svg {
        opacity: 1;
        color: #ffa959;
      }
    }
  }
}

.back-link {
  display: flex;
  align-items: flex-end;
  position: fixed;
  top: 120px;
  left: 0;
  z-index: 7;
  .line {
    width: 44px;
    height: 1px;
    background-color: $white;
    margin-bottom: 6px;
  }
  a {
    margin-left: 16px;
    font-size: 12px;
    line-height: 1.66;
    color: $white;
  }
}

.button {
  @include center;
  padding: 10px 15px;
  border-radius: 8px;
  background-color: $blue;
  font-size: 1em;
  line-height: 1.6;
  color: $black;
  font-weight: 600;
  letter-spacing: -0.24px;
  cursor: pointer;
  @include w($sm) {
    font-size: 0.8em;
    line-height: 2;
  }
  &_transp-hover {
    @include center;
    padding: 8px 15px;
    border-radius: 8px;
    background-color: transparent;
    border: 2px solid $blue;
    font-size: 1em;
    line-height: 1.6;
    color: $blue;
    font-weight: 600;
    letter-spacing: -0.24px;
    cursor: pointer;
    transition: $anim;
    @include offHoverOnDevices {
      &:hover {
        background-color: $blue;
        color: $black;
      }
    }
    @include w($sm) {
      font-size: 0.8em;
      line-height: 2;
    }
  }
  svg {
    margin-right: 10px;
  }
  &-filter {
    @include center;
    padding: 7px 24px;
    border-radius: 48px;
    background-color: rgba($blue, 0.2);
    font-size: 1em;
    line-height: 1.6;
    color: $blue;
    font-weight: 600;
    letter-spacing: -0.24px;
    cursor: pointer;
    transition: $anim;
    @include w($sm) {
      font-size: 0.8em;
      line-height: 2;
    }
    &.active {
      background-color: $blue;
      color: $black;
    }
  }
  &.disabled {
    opacity: 0.5;
    pointer-events: none;
    cursor: default;
  }
  &_red {
    background-color: $red;
    color: $white;
  }
}

// static aside section
.section-aside {
  position: absolute;
  top: 0;
  right: 0;
  width: 255px;
  height: 100%;
  z-index: 5;
  &__items {
    height: 100%;
  }
  &__item {
    height: 33.333%;
    border-bottom: 1px solid rgba($white, 0.1);
    &_show-md {
      display: none;
    }
    &:last-child {
      border-bottom: none;
    }
  }
  .page-link {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    position: relative;
    padding: 20px;
    @include offHoverOnDevices {
      &:hover .page-link__bg {
        opacity: 1;
      }
    }
    &__bg {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background: radial-gradient(
        100% 104.04% at 100% 50%,
        #170d40 0%,
        #1b1148 100%
      );
      opacity: 0.6;
      transition: opacity 0.3s;
    }
    &__icon {
      color: $blue;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
    &__text {
      position: relative;
      @include text-white15;
    }
    &__caption {
      font-weight: 800;
    }
  }
  .media-one {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    position: relative;
    padding: 20px;
    &.game-one {
      padding: 28px 18px;
      .media-one {
        &__desc {
          display: flex;
          align-items: center;
          position: relative;
        }
        &__caption {
          margin-top: 0;
          margin-left: 8px;
        }
      }
      .icon-counter-strike {
        font-size: 26px;
      }
      .icon-dota-2 {
        font-size: 16px;
      }
      .icon-mortal-kombat {
        font-size: 21px;
      }
      & > a {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
      }
    }
    &_coming {
      background-color: $violet3;
      justify-content: space-between;
      p {
        @include text-white25-eb;
      }
      &.game-one {
        padding: 24px 16px;
      }
    }
    &__bg-img {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      &::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        bottom: 0;
        background: linear-gradient(
          180deg,
          rgba(23, 13, 64, 0) 21%,
          #170d40 68.8%
        );
      }
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &__icon {
      color: $white;
      position: absolute;
      top: 36.8%;
      left: 50%;
      transform: translate(-50%, -50%);
      cursor: pointer;
      @include center;
    }
    &__text {
      position: relative;
    }
    &__caption {
      font-size: 1em;
      line-height: 1.6;
      letter-spacing: -0.24px;
      font-weight: 800;
      @include text-limit(1);
      margin-top: 5px;
    }
    &__desc {
      img {
        width: 20px;
        height: 20px;
        object-fit: cover;
      }
    }
  }
  .icon-play {
    font-size: 28px;
  }
  .news-info {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    position: relative;
    padding: 20px;
    &_coming {
      background-color: $violet3;
      justify-content: space-between;
      p {
        @include text-white25-eb;
      }
    }
    & > a {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
    }
    &__bg-img {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      &::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        bottom: 0;
        background: linear-gradient(
          180deg,
          rgba(23, 13, 64, 0) 21%,
          #170d40 68.8%
        );
      }
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &__main {
      position: relative;
    }
    &__top {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__action {
      margin-left: 10px;
      @include center;
    }
    &__caption {
      font-size: 1em;
      line-height: 1.6;
      letter-spacing: -0.24px;
      font-weight: 800;
      margin-top: 5px;
      @include text-limit(1);
    }
    &__bottom {
      display: flex;
      justify-content: space-between;
      margin-top: 8px;
      @include text-white10;
    }
    &__reading {
      margin-left: 10px;
    }
  }
  &__social-feed {
    width: 100%;
    height: 100%;
    overflow: hidden;
    overflow-y: auto;
  }
}
// /static aside section

.notification {
  position: fixed;
  top: 72px;
  right: 60px;
  z-index: 100;
  @include text-white15;
  background-color: $blue;
  border-radius: 8px;
  padding: 16px 60px 16px 16px;
  color: $violet1;
  &::after {
    content: '';
    position: absolute;
    right: 50px;
    bottom: 100%;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 5px 10px;
    border-color: transparent transparent $blue;
  }
  &__close-btn {
    @include center;
    position: absolute;
    top: 10px;
    right: 10px;
  }
  svg {
    font-size: 10px;
  }
}

.title-block {
  display: flex;
  justify-content: space-between;
  align-items: center;
  &__link {
    display: flex;
    align-items: center;
    svg {
      margin-right: 8px;
      color: $blue;
    }
    span {
      @include text-white15;
      color: $blue;
      font-weight: 700;
    }
  }
}

// === dropdown list styles ===
.__select {
  position: relative;
  width: 100%;
  height: 32px;
  border-radius: 8px;
  background-color: $white;
  border: 1px solid $border;
  font-size: 0.9333em;
  line-height: 1.52;
  color: $violet2;
  margin: 0 auto;
  &[data-state='active'] {
    & svg {
      transform: rotateX(-180deg);
    }
    .__select__content {
      opacity: 1;
      z-index: 8;
    }
    .__select__input + .__select__label {
      max-height: 32px;
    }
  }
  svg {
    position: absolute;
    top: 11px;
    right: 13px;
    font-size: 10px;
    color: #706b85;
    transition: all 0.3s ease-in-out;
  }
  &__title {
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 5px 30px 5px 12px;
    border-radius: 8px;
    cursor: pointer;
    // &::after {
    //   content: '';
    //   display: block;
    //   height: 5px;
    //   margin-top: -4px;
    //   pointer-events: none;
    //   position: absolute;
    //   border-style: solid;
    //   border-width: 4px 4px 0;
    //   border-color: #24292e transparent transparent;
    //   transform: rotate(0);
    //   transform-origin: unset;
    //   top: 50%;
    //   right: 20px;
    //   transition: all 0.3s ease-in-out;
    //   width: 5px;
    // }
  }
  &__content {
    width: 100%;
    box-shadow: 0 0 0 1px rgba(68, 68, 68, 0.11);
    margin-top: 4px;
    border-radius: 4px;
    position: absolute;
    top: 100%;
    left: 0;
    max-height: 250px;
    display: flex;
    flex-direction: column;
    background-color: $white;
    transition: all 0.3s ease-out;
    opacity: 0;
    z-index: -1;
  }
  &__input {
    display: none;
    &:checked + label {
      font-weight: 700;
    }
    &:disabled + label {
      opacity: 0.6;
      pointer-events: none;
    }
  }
  &__label {
    display: flex;
    align-items: center;
    width: 100%;
    height: 32px;
    max-height: 0;
    padding: 0 12px;
    flex-shrink: 0;
    transition: all 0.2s ease-out;
    cursor: pointer;
    overflow: hidden;
    @include offHoverOnDevices {
      &:hover {
        background-color: #f6f6f6;
      }
    }
  }
}
// === /dropdown list styles ===

.searching {
  &__close-btn {
    @include center;
    position: absolute;
    top: 24px;
    right: 3.1vw;
    svg {
      color: $white;
      font-size: 16px;
    }
  }
  &__form {
    display: flex;
    width: 100%;
    margin-top: 65px;
    padding: 8px 0;
    border-bottom: 1px solid rgba($white, 0.7);
    &-input {
      width: 100%;
      padding-right: 10px;
      flex-grow: 1;
      font-size: 1.667em;
      line-height: 1;
      font-weight: 600;
      color: $white;
      letter-spacing: -0.24px;
      &::placeholder {
        opacity: 0.4;
      }
    }
    &-btn {
      @include center;
      color: $blue;
      svg {
        font-size: 32px;
      }
    }
  }
}

.search-results {
  &__title {
    padding-top: 90px;
  }
  &__section {
    .container-md3 {
      margin: 0;
    }
    .title-h2 {
      padding-top: 80px;
    }
    .latest-news {
      padding-top: 30px;
    }
    &_no-result {
      .title-h2 {
        padding-top: 150px;
      }
    }
  }
}

// media
@include w($md) {
  .full-height {
    height: auto;
  }
  main {
    padding: 30px 0 70px;
  }
  .container {
    padding-left: $sm-p;
    padding-right: $sm-p;
    &-md2,
    &-md3 {
      margin: 0 auto;
    }
    &-sides-lg {
      padding-left: $sm-p;
      padding-right: $sm-p;
    }
  }
  .social {
    display: none;
  }
  .tournaments-submenu-block {
    position: fixed;
    bottom: 0;
    top: initial;
    left: 0;
    width: 100%;
    height: 55px;
    background-color: #382e61;
    z-index: 10;
    transition: 0.3s;
    &.hide {
      transform: translateY(100%);
    }
    .sticky-block {
      position: static;
      height: 100%;
      flex-direction: row;
      justify-content: space-between;
    }
    .caption {
      display: none;
    }
    .games-list {
      display: flex;
      height: 100%;
      a {
        width: 100px;
        height: 100%;
        border-bottom: none;
        border-right: 1px solid rgba(255, 255, 255, 0.1);
      }
    }
    .tournaments-submenu {
      display: flex;
      height: 100%;
      li {
        &:nth-child(1) {
          a {
            border-top: none;
          }
        }
      }
      a {
        width: 100px;
        height: 100%;
        border-bottom: none;
        border-left: 1px solid rgba(255, 255, 255, 0.1);
      }
    }
  }
  .back-link {
    position: static;
    margin-bottom: 50px;
    margin-left: -15px;
  }
  .page-numbering {
    display: none;
  }
  .section-aside {
    .page-link {
      padding: 26px;
    }
    .icon-play {
      font-size: 57px;
    }
    &__item {
      &_show-md {
        display: block;
      }
    }
    .media-one {
      position: absolute;
      top: 0;
      left: 0;
      &.game-one {
        padding: 24px 16px;
      }
      &__caption {
        margin-top: 8px;
      }
    }
    .news-info {
      position: absolute;
      top: 0;
      left: 0;
      padding: 16px;
    }
  }
  .notification {
    top: 93px;
    right: 42px;
  }
  .searching {
    &__close-btn {
      right: $sm-p;
    }
  }
  .search-results {
    &__title {
      padding-top: 40px;
    }
    &__section {
      .title-h2 {
        padding-top: 70px;
      }
      &_no-result {
        .title-h2 {
          padding-top: 100px;
        }
      }
    }
  }
}

@include w($sm) {
  main {
    padding: 25px 0 45px;
  }
  .stop-scroll {
    overflow: hidden;
  }
  .container {
    padding-left: $sm-p;
    padding-right: $sm-p;
    &-sides-lg {
      padding-left: $sm-p;
      padding-right: $sm-p;
    }
  }
  .tournaments-submenu-block {
    .games-list {
      width: 100%;
      li {
        width: 33.33%;
        &:last-child {
          a {
            border-right: none;
          }
        }
      }
      a {
        width: 100%;
        height: 100%;
        border-bottom: none;
        border-right: 1px solid rgba(255, 255, 255, 0.1);
      }
    }
    .tournaments-submenu {
      display: none;
    }
  }
  .back-link {
    margin-left: -$sm-p;
    margin-bottom: 35px;
  }
  .section-aside {
    .media-one {
      padding: 16px;
      &.game-one {
        .icon-counter-strike {
          font-size: 30px;
        }
      }
      &__caption {
        margin-top: 2px;
      }
    }
  }
  .notification {
    padding-right: 45px;
    top: 79px;
    right: 10px;
    &::after {
      right: 7px;
    }
    &__close-btn {
      top: 12px;
      right: 12px;
    }
    svg {
      font-size: 16px;
    }
  }
  .title-block {
    display: block;
    .button {
      margin-top: 24px;
    }
    &__link {
      margin-top: 16px;
    }
  }
  .search-results {
    &__title {
      padding-top: 20px;
    }
    &__section {
      .title-h2 {
        padding-top: 45px;
      }
      &_no-result {
        .title-h2 {
          padding-top: 60px;
        }
      }
    }
  }
}
