.authorization {
  padding: 0;
  &__wrapper {
    min-height: 100vh;
    display: flex;
  }
  &__entering-block {
    flex-shrink: 0;
    width: 100%;
    max-width: 432px;
  }
  &__img {
    position: relative;
    width: 100%;
    &::after {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      bottom: 0;
      background: linear-gradient(
        180deg,
        rgba(23, 13, 64, 0) 10.37%,
        #011427 95%
      );
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

.entering-block {
  display: flex;
  flex-direction: column;
  padding: 18px 58px 34px;
  &__logo {
    align-self: flex-start;
  }
  &__main {
    margin-top: 13vh;
  }
  &__title {
    font-family: 'SF Pro Display';
    font-size: 1.34em;
    line-height: 1.2;
    font-weight: 700;
    text-align: center;
    padding-bottom: 35px;
  }
  &__subtitle {
    font-family: 'SF Pro Display';
    font-size: 0.935em;
    line-height: 1.43;
    font-weight: 600;
    margin-bottom: 30px;
  }
  &__menu-title {
    .tabs-menu-item {
      width: 50%;
      text-align: center;
      padding: 0 12px 8px;
      border-bottom: 1px solid transparent;
      font-family: 'SF Pro Display';
      font-size: 0.94em;
      line-height: 1.43;
      font-weight: 600;
      &.current {
        color: $blue;
        border-bottom: 1px solid $blue;
      }
    }
  }
  &__form {
    padding-top: 45px;
    .form-actions {
      margin-top: 50px;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    .button {
      min-width: 136px;
      padding: 8px 15px;
      &_transp-hover {
        min-width: auto;
        padding: 6px 22px;
      }
    }
  }
  &__bottom {
    margin-top: auto;
    padding-top: 50px;
    p {
      opacity: 0.5;
      margin-top: auto;
      font-size: 0.8em;
      line-height: 1.34;
      margin-bottom: 1em;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

.field-wrapper {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;
  position: relative;
  // &:last-child {
  //   margin-bottom: 0;
  // }
  label {
    align-self: flex-start;
    margin-bottom: 4px;
    font-family: 'SF Pro Display';
    font-size: 0.94em;
    line-height: 1.43;
  }
  input {
    font-family: 'SF Pro Display';
    font-size: 0.94em;
    line-height: 1;
    border-radius: 4px;
    padding: 11px 12px;
    border: 1px solid rgba(#e5e6e9, 0.2);
    &:focus {
      border: 1px solid #e5e6e9;
    }
  }
  &-agree {
    display: flex;
    input[type='checkbox'] {
      &:checked + label::before {
        background: url('../images/check-white.svg') center no-repeat, $blue;
      }
    }
    label {
      display: flex;
      align-items: center;
      cursor: pointer;
      font-family: 'SF Pro Display';
      font-size: 0.94em;
      line-height: 1;
      &::before {
        display: block;
        content: '';
        width: 16px;
        height: 16px;
        border-radius: 2px;
        border: 1px solid $border;
        margin-right: 8px;
      }
    }
  }
  &-code-inputs {
    display: flex;
    input {
      text-align: center;
      width: 40px;
      margin-right: 24px;
      &:last-child {
        margin-right: 0;
      }
    }
  }
  .form-message {
    margin-top: 12px;
  }
  .eye {
    width: 24px;
    height: 24px;
    cursor: pointer;
    position: absolute;
    bottom: 8px;
    right: 12px;
    background: url('../images/eye-close.svg') center/contain no-repeat;
    &.eye-close {
      background: url('../images/eye-open.svg') center/contain no-repeat;
    }
  }
}

.underline-link {
  font-family: 'SF Pro Display';
  font-size: 12px;
  line-height: 1.33;
  border-bottom: 1px solid $white;
  transition: $anim;
  @include offHoverOnDevices {
    &:hover {
      border-bottom: 1px solid transparent;
    }
  }
}

.progress-bar {
  width: 100%;
  height: 2px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  &::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: $white;
    opacity: 0.4;
    border-radius: 2px;
    width: 100%;
    height: 2px;
  }
  &__passed {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0;
    height: 2px;
    background-color: $blue;
    &_50 {
      width: 50%;
    }
    &_100 {
      width: 100%;
    }
  }
  &__item {
    width: 10px;
    height: 10px;
    background-color: #c4c4c4;
    border-radius: 50%;
    position: relative;
    &.active {
      background-color: $blue;
    }
  }
}

.form-message {
  font-family: 'SF Pro Display';
  font-size: 14px;
  line-height: 1.43;
  .underline-link {
    font-size: 14px;
    line-height: 1.43;
  }
}

// media
@include w($md) {
  .authorization {
    &__entering-block {
      max-width: 375px;
    }
  }
  .entering-block {
    padding: 18px 15px 24px;
  }
}

@include w($sm) {
  .entering-block {
    padding: 23px 10px 24px;
    &__logo {
      img {
        width: 116px;
      }
    }
    &__main {
      margin-top: 45px;
    }
    &__subtitle {
      font-size: 0.8em;
      line-height: 1.67;
    }
    &__menu-title {
      .tabs-menu-item {
        font-size: 0.8em;
        line-height: 1.67;
      }
    }
    &__form {
      .button {
        font-size: 0.866em;
        line-height: 1.85;
      }
    }
  }
  .field-wrapper {
    label {
      margin-bottom: 12px;
      font-size: 0.8em;
      line-height: 1;
    }
    &-agree {
      label {
        font-size: 0.8em;
      }
    }
  }
  .form-message {
    font-size: 12px;
    line-height: 1.67;
    .underline-link {
      font-size: 12px;
      line-height: 1.67;
    }
  }
}
