main {
  &.homepage {
    padding: 0;
  }
}

.homepage {
  .container-sides-lg {
    height: 100%;
  }
  .section-aside {
    padding-top: 70px;
  }
  .subscribe-form__submit {
    color: $white;
  }
  .main-section {
    height: 100vh;
    min-height: 700px;
  }
}

.page-slider {
  &.swiper-container {
    height: 100%;
  }
  section {
    box-sizing: border-box;
  }
}

.main-section {
  width: 100%;
  position: relative;
}

// main slider
.main-page-slider {
  width: 100%;
  height: 100%;
  position: relative;
  .swiper-container {
    height: 100%;
  }
  .pagination-wrapper {
    display: flex;
    align-items: center;
    position: absolute;
    bottom: 3.7em;
    right: 26.5em;
  }
  .swiper-pagination {
    display: flex;
    position: static;
    margin: 0 2em;
    z-index: 1;
  }
  .swiper-button-prev,
  .swiper-button-next {
    position: static;
    width: auto;
    height: auto;
    margin-top: 0;
    z-index: 1;
    &::after {
      content: none;
    }
  }
  .swiper-pagination-bullet {
    width: 10px;
    height: 10px;
    margin: 0 7px;
    background-color: $white;
    cursor: pointer;
    &-active {
      background-color: $blue;
    }
  }
  .swiper-slide {
    box-sizing: border-box;
    &-active {
      .slide-main {
        &__bg {
          // animation: name duration timing-function delay iteration-count direction fill-mode;
          animation: image-scaling 5.5s linear;
        }
        &__body {
          opacity: 1;
          transform: translateY(0);
        }
      }
    }
  }
}

// /main slider

.slide-main {
  width: 100%;
  height: 100%;
  position: relative;
  &__bg {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    &::after {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      bottom: 0;
      background: linear-gradient(
        180deg,
        rgba(23, 13, 64, 0) 10.37%,
        #011427 95%
      );
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__body {
    width: 100%;
    max-width: 533px;
    height: 100%;
    margin-left: 30px;
    display: flex;
    align-items: center;
    position: relative;
    padding: 80px 0;

    opacity: 0;
    transform: translateY(2em);
    transition: transform 0.4s 0.75s, opacity 0.4s 0.75s;
    .detail {
      position: absolute;
      bottom: 51px;
      left: 0;
      @include display-blue20-s;
    }
  }
  &__content {
    display: flex;
    flex-direction: column;

    .title {
      h1 {
        width: 100%;
        max-width: 7em;
        @include display-white60-m;
        margin-top: 10px;
      }
      p {
        width: 100%;
        max-width: 7em;
        @include display-white60-m;
        margin-top: 10px;
        // text-decoration: underline rgba($white, 0);
        // transition: text-decoration-color $anim;
        // @include offHoverOnDevices {
        //   &:hover {
        //     text-decoration-color: rgba($white, 1);
        //   }
        // }
      }
      em {
        color: $blue;
        font-weight: 800;
      }
    }
    & > p {
      width: 100%;
      max-width: 32em;
      @include text-white15;
      margin-top: 30px;
    }
  }
}

@keyframes image-scaling {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.1);
  }
}

// section with videos
.homepage-media {
  position: relative;
  .background-img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    // background: linear-gradient(0deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
    //   url('../images/image-10.jpg') center/cover no-repeat;
    &::after {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      bottom: 0;
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4));
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__main {
    position: relative;
    height: 100%;
    padding: 80px 0 45px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .content {
      &-cols-wrapper {
        display: flex;
        justify-content: space-around;
        flex-grow: 1;
      }
      &-left {
        margin-left: 30px;
        flex-basis: 533px;
        display: flex;
        flex-direction: column;
      }
      &-right {
        margin-left: 30px;
        margin-right: 30px;
        flex-basis: 420px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
      }
      &-bottom {
        display: flex;
        justify-content: space-around;
        &__left {
          margin-left: 30px;
          flex-basis: 533px;
          display: flex;
          align-items: flex-end;
        }
        &__right {
          margin-left: 30px;
          margin-right: 30px;
          flex-basis: 420px;
          display: flex;
        }
      }
    }
    .play-btn {
      a {
        display: inline-flex;
        align-items: center;
        @include text-blue20-s;
      }
      svg {
        margin-right: 20px;
        color: $white;
      }
    }
    .video-list {
      padding-top: 10px;
      li {
        padding: 20px 0;
        border-bottom: 1px solid rgba($white, 0.4);
        &:last-child {
          border-bottom: none;
        }
      }
      &__link {
        display: inline-flex;
        align-items: flex-start;
        @include text-white15;
      }
      &__icon {
        width: 21px;
        height: 14px;
        flex-shrink: 0;
        margin-top: 5px;
        @include center;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      &__desc {
        margin-left: 15px;
      }
      &__title {
        font-size: 15px;
        line-height: 1.6;
        letter-spacing: -0.24px;
        font-weight: 800;
      }
    }
    .current-video {
      p {
        margin-top: 5px;
        @include text-white15;
        &.current-video__title {
          font-weight: 800;
          font-size: 15px;
          line-height: 1.6;
          letter-spacing: -0.24px;
        }
      }
    }
  }
  &__desc {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  &__text {
    width: 100%;
    max-width: 32em;
    @include text-white15;
    margin-top: 30px;
  }
}

// news section
.homepage-news {
  background-color: $violet2;
  position: relative;
  &__main {
    max-width: 870px;
    height: 100%;
    padding: 70px 0 4.8vh;
    margin-left: 30px;
    margin-right: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    .news-info {
      &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
      &__action {
        margin-left: 50px;
        @include center;
        align-items: flex-start;
      }
      &__caption {
        @include display-white25-eb;
        font-family: 'SF Pro Text';
        margin-top: 10px;
        @include text-limit(2);
      }
      &__bottom {
        display: flex;
        justify-content: space-between;
        margin-top: 15px;
        @include text-white10;
      }
      &__reading {
        margin-left: 10px;
      }
    }
  }
  &__title-block {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-top: 3.7vh;
    a {
      margin-bottom: 6px;
      margin-left: 15px;
      @include text-blue20-s;
      flex-shrink: 0;
    }
  }
  &__desc {
    height: 48vh;
    margin-top: 15px;
    p {
      @include text-white17;
      padding-top: 0.5em;
      padding-bottom: 0.5em;
    }
  }
  &__banner {
    width: 100%;
    height: 100%;
    border-radius: 12px;
    overflow: hidden;
    img {
      height: 100%;
      width: 100%;
      object-fit: contain;
      object-position: left;
    }
  }
  &__news-info {
    margin-top: 4.3vh;
    max-width: 535px;
    margin-top: 15px;
  }
}

// news section slider
.news-slider {
  width: 100%;
  height: 100%;
  position: relative;
  &.swiper-container {
