// /news section slider

// media
@include w($md) {
  .main-section {
    .section-aside {
      width: 225px;
    }
  }
  .slide-main {
    &__body {
      display: block;
      padding-top: 70px;
      margin-left: 0;
      .detail {
        bottom: 6.8vh;
      }
    }
    &__content {
      margin-top: 10vh;
      & > p {
        max-width: 24em;
      }
    }
  }
  .main-page-slider {
    .pagination-wrapper {
      left: 50%;
      right: initial;
      bottom: 6.8vh;
      transform: translateX(-50%);
      z-index: 1;
    }
  }
  .homepage-media {
    .background-img {
      max-height: 950px;
    }
    &__main {
      padding: 70px 0 40px;
      .content {
        &-cols-wrapper {
          display: block;
        }
        &-left {
          margin-left: 0;
        }
        &-right {
          margin: 25px 0 0;
          display: block;
          max-width: 360px;
        }
        &-bottom {
          justify-content: space-between;
          flex-wrap: wrap;
          margin-top: 25px;
          &__left {
            margin-left: 0;
            flex-basis: 50%;
            align-items: center;
          }
          &__right {
            margin: 0;
            flex-basis: 50%;
          }
        }
      }
      .video-list {
        padding-top: 0;
      }
    }
    &__desc {
      display: block;
      max-width: 533px;
    }
    &__text {
      margin-top: 16px;
    }
    .section-aside {
      position: static;
      width: auto;
      height: auto;
      z-index: initial;
      padding-top: 0;
      padding-bottom: 45px;
      &__items {
        height: auto;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 30px 20px;
      }
      &__item {
        position: relative;
        padding-top: 100%;
        height: 100%;
        border-bottom: none;
      }
    }
  }
  .homepage-news {
    .section-aside {
      position: static;
      width: auto;
      height: auto;
      z-index: initial;
      padding: 0 15px 45px;
      &__items {
        height: auto;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 30px 20px;
      }
      &__item {
        position: relative;
        padding-top: 100%;
        height: 100%;
        border-bottom: none;
      }
    }
    &__main {
      max-width: none;
      padding-bottom: 45px;
      margin-left: 0;
      margin-right: 0;
      .news-info {
        &__action {
          svg {
            font-size: 24px;
          }
        }
      }
    }
    &__title-block {
      align-items: initial;
      margin-top: 0;
      a {
        margin-top: 20px;
        margin-left: 50px;
      }
    }
    &__desc {
      margin-top: 45px;
      width: 100%;
      height: auto;
    }
    &__news-info {
      max-width: 360px;
      margin-top: 30px;
    }
  }
  .news-slider {
    .pagination-wrapper {
      right: 15px;
    }
  }
}

@include w(992) {
  .homepage-media {
    .section-aside {
      &__items {
        grid-template-columns: repeat(2, 1fr);
      }
    }
  }
  .homepage-news {
    .section-aside {
      &__items {
        grid-template-columns: repeat(2, 1fr);
      }
    }
  }
}

@include w($sm) {
  .homepage {
    .main-section {
      min-height: 500px;
    }
  }
  .main-section {
    .section-aside {
      display: none;
    }
  }
  .slide-main {
    &__body {
      .detail {
        position: static;
      }
    }
    &__content {
      margin-top: 6.4vh;
      margin-bottom: 40px;
      & > p {
        margin-top: 16px;
      }
    }
  }
  .homepage-media {
    .background-img {
      max-height: 690px;
    }
    &__main {
      padding: 40px 0 20px;
      .content {
        &-right {
          margin-top: 14px;
        }
        &-bottom {
          margin-top: 30px;
          &__left {
            flex-basis: 100%;
          }
          &__right {
            flex-basis: 100%;
            padding-top: 22px;
          }
        }
      }
      .video-list {
        li {
          padding: 16px 0;
        }
      }
      .play-btn {
        svg {
          margin-right: 8px;
          font-size: 32px;
        }
      }
    }
    .section-aside {
      padding-top: 40px;
    }
  }
  .homepage-news {
    .section-aside {
      padding-left: 10px;
      padding-right: 10px;
    }
    &__main {
      padding: 40px 0 90px;
      .news-info {
        &__caption {
          margin-top: 16px;
        }
        &__bottom {
          margin-top: 8px;
        }
      }
    }
    &__title-block {
      display: block;
      a {
        display: inline-block;
        margin-left: 0;
        margin-top: 8px;
      }
    }
    &__desc {
      margin-top: 30px;
    }
    &__news-info {
      margin-top: 20px;
      max-width: none;
    }
  }
  .news-slider {
    .pagination-wrapper {
      right: initial;
      left: 50%;
      bottom: 40px;
      transform: translateX(-50%);
    }
  }
}

@include w(640) {
  .homepage-media {
    .section-aside {
      &__items {
        grid-template-columns: 1fr;
      }
    }
  }
  .homepage-news {
    .section-aside {
      &__items {
        grid-template-columns: 1fr;
      }
    }
  }
}
