// ===================fonts=====================
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap');

$ff: 'Roboto', sans-serif;
// ===================media=====================
$md: 1250;
$sm: 768;

$sm-p: 15px;

$anim: 0.2s ease;

// ===================colors=====================
$white: #ffffff;
$blue: #16d5e1;
$violet1: #170d40;
$violet2: #22174f;
$violet3: #2d2358;
$black: #110833;
$background: $violet2;
$border: #706b85;
$border-light: rgba(#ffffff, 0.2);
$yellow: #ffa959;
$red: #ff4848;

// ========================mixin==============================
@function width($col, $margin: 0px) {
  @return calc((100% / #{$col}) - #{$margin});
}

// how use
// @include fluid-type($min_width, $max_width, 12px, 16px);
// /how use

@function strip-unit($value) {
  @return $value / ($value * 0 + 1);
}

@mixin fluid-type($min-vw, $max-vw, $min-font-size, $max-font-size) {
  $u1: unit($min-vw);
  $u2: unit($max-vw);
  $u3: unit($min-font-size);
  $u4: unit($max-font-size);

  @if $u1 == $u2 and $u1 == $u3 and $u1 == $u4 {
    & {
      font-size: $min-font-size;
      @media screen and (min-width: $min-vw) {
        font-size: calc(
          #{$min-font-size} + #{strip-unit($max-font-size - $min-font-size)} *
            ((100vw - #{$min-vw}) / #{strip-unit($max-vw - $min-vw)})
        );
      }
      @media screen and (min-width: $max-vw) {
        font-size: $max-font-size;
      }
    }
  }
}

@mixin center {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin text-limit($row) {
  display: -webkit-box;
  text-overflow: ellipsis;
  overflow: hidden;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: $row;
}

@mixin abs {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
}

@mixin triangle($color) {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 5px 5px 0 5px;
  border-color: $color transparent transparent transparent;
}

@mixin fixInputIOS {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

@mixin w($size, $sizeMin: 0) {
  @if ($sizeMin == 0) {
    @media (max-width: $size - 0.02 +'px') {
      @content;
    }
  } @else {
    @media (min-width: $sizeMin + 'px') and (max-width: $size - 0.02 +'px') {
      @content;
    }
  }
}

@mixin h($size) {
  @media (max-height: $size +'px') {
    @content;
  }
}

@mixin offHoverOnDevices {
  @media screen and (min-width: 1200px) {
    @content;
  }
}

@mixin tableColWidth($sizes...) {
  @for $i from 0 to length($sizes) {
    &:nth-child(#{$i + 1}) {
      width: #{nth($sizes, $i + 1)};
    }
  }
}

@mixin minw($size) {
  @media (min-width: $size + 'px') {
    @content;
  }
}

@mixin img-full($fit: contain) {
  @include center;
  overflow: hidden;
  img {
    width: 100%;
    height: 100%;
    object-fit: $fit;
    flex-shrink: 0;
  }
}

@mixin img {
  @include center;
  img {
    flex-shrink: 0;
    width: auto;
    height: auto;
    max-width: 100%;
    max-height: 100%;
  }
}

