.tournament {
  &-short-info {
    &__wrapper {
      position: absolute;
      top: 0;
      right: 0;
      width: 420px;
      height: 100%;
    }
    &__block {
      position: sticky;
      top: 100px;
      padding: 20px 16px;
      border-radius: 8px;
      background-color: rgba($white, 0.1);
    }
    &__logo {
      width: 48px;
      height: 48px;
      border-radius: 50%;
      overflow: hidden;
      grid-row: 1/3;
    }
    &__title {
      @include text-white15-eb;
      word-break: break-word;
    }
    &__mode {
      @include text-white15;
    }
    &__status {
      @include text-white15-eb;
      &_current {
        color: #28ffbf;
      }
      &_future {
        color: #8f71ff;
      }
      &_completed {
        color: #e1e6f0;
      }
      &_canceled {
        color: #ff1967;
      }
    }
    &__date {
      margin-top: 4px;
      @include text-white15;
    }
    &__participants {
      display: flex;
      align-items: center;
      svg {
        margin-right: 8px;
      }
      span {
        @include text-white15-s;
      }
    }
    &__acces-type {
      color: $blue;
    }
    &__award {
      display: flex;
      align-items: center;
      svg {
        margin-right: 8px;
      }
      p {
        @include text-white15-s;
      }
    }
    &__row {
      display: grid;
      grid-template-columns: auto 1fr;
      grid-gap: 25px 40px;
      &_full {
        grid-template-columns: 1fr;
      }
      &:nth-child(2) {
        margin-top: 20px;
      }
      &:nth-child(3) {
        margin-top: 30px;
      }
    }
    &__col {
      &_title {
        max-width: 174px;
        display: grid;
        grid-template-columns: max-content auto;
        // align-items: center;
        align-items: start;
        grid-column-gap: 15px;
      }
    }
  }
  &-desc-wrap {
    position: relative;
    margin-top: 45px;
  }
  &-desc-col {
    width: 100%;
    max-width: 645px;
  }
  &__top-img {
    width: 100%;
    height: 0;
    padding-top: 61.5%;
    position: relative;
    border-radius: 8px;
    overflow: hidden;
    margin-bottom: 30px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  &__short-info {
    &-wrapper {
      position: absolute;
      top: 0;
      right: 0;
      width: 420px;
      height: 100%;
    }
  }
  &-about {
    &__title-h3 {
      margin: 45px 0 20px;
    }
    &__table {
      display: grid;
      grid-template-columns: 200px 1fr;
      grid-gap: 24px 30px;
    }
    &__col {
      display: flex;
      align-items: center;
    }
    &__caption {
      display: flex;
      align-items: center;
      @include text-white15;
      opacity: 0.8;
    }
    &__icon {
      display: inline-flex;
      width: 24px;
      margin-right: 8px;
    }
    &__desc {
      display: inline-block;
      @include text-white15-eb;
    }
  }
  &__info-section {
    padding-top: 80px;
    .title-h2 {
      margin-bottom: 30px;
    }
  }
  &__maps-pool {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 24px 30px;
  }
  &__map {
    &-img {
      width: 100%;
      height: 0;
      position: relative;
      padding-top: 56.7%;
      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &-name {
      margin-top: 8px;
      @include text-white15;
    }
  }
  &__desc-wrap {
    p {
      @include text-white15;
      margin-bottom: 1em;
    }
    ul {
      margin-bottom: 10px;
    }
    li {
      @include text-white15;
      margin-bottom: 4px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    a {
      @include text-white15;
      color: $blue;
    }
  }
  &__teams-wrap {
    border-radius: 8px;
    background-color: rgba($white, 0.1);
  }
  &__all-teams-btn {
    margin-top: 24px;
    padding-left: 37px;
    padding-right: 37px;
  }
}

.team-item {
  display: flex;
  align-items: center;
  padding: 16px 32px;
  border-bottom: 1px solid rgba($white, 0.1);
  &:last-child {
    border-bottom: none;
  }
  &__team {
    display: flex;
    align-items: center;
  }
  &__logo {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__name {
    margin-left: 25px;
    @include text-white15;
  }
  &__profile {
    margin-left: auto;
    @include text-white15;
    color: $blue;
  }
}

.standings {
  padding-top: 80px;
  &__header-top {
    display: flex;
    align-items: flex-end;
  }
  //   &__status-block {
  //     display: flex;
  //     margin-left: 96px;
  //   }
  &__status-item {
    display: flex;
    align-items: center;
    @include text-white15;
    margin-right: 24px;
    &:nth-child(2) {
      margin-left: 96px;
    }
    &:nth-child(4) {
      margin-right: auto;
    }
    svg {
      margin-right: 8px;
    }
    &_succes {
      svg {
        color: #28ffbf;
      }
    }
    &_cancel {
      svg {
        color: #ff5d9e;
      }
    }
    &_tech-defeat {
      svg {
        color: #8f71ff;
      }
    }
  }
  &__controls {
    // display: flex;
    // margin-left: auto;
    &-item {
      display: flex;
      align-items: center;
      @include text-white15;
      color: $blue;
      margin-right: 24px;
      &:last-child {
        margin-right: 0;
      }
      svg {
        margin-right: 8px;
      }
    }
  }
  &__top-labels {
    display: inline-flex;
    background-color: $violet2;
    // padding-top: 30px;
    padding-left: 8px;
  }
  &__label-item {
    width: 225px;
    min-width: 225px;
    padding-top: 8px;
    padding-bottom: 26px;
    padding-right: 30px;
    position: relative;
    &.highlighted {
      &::before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        left: -8px;
        width: calc(100% - 14px);
        height: 100%;
        background-color: #382e61;
      }
    }
    p {
      font-size: 1em;
      line-height: 1.6;
      color: $white;
      font-weight: 400;
      letter-spacing: -0.24px;
      position: relative;
      &:nth-child(1) {
        font-weight: 800;
        margin-bottom: 4px;
      }
      &:nth-child(2) {
        opacity: 0.5;
      }
    }
  }
  &__body {
    margin-top: 30px;
    // cursor: grab;
    overflow-x: auto;
    background-color: #2d2358;
    .fullscreen-off-btn {
      position: fixed;
      bottom: 20px;
      right: 20px;
      z-index: 10;
      color: #16d5e1;
      font-size: 1em;
      line-height: 1.6;
      font-weight: 600;
      letter-spacing: -0.24px;
      padding: 3px 14px;
      border-radius: 8px;
      background-color: #1f3d6c;
      @include center;
      display: none;
      svg {
        margin-right: 9px;
      }
      @include offHoverOnDevices {
        transition: 0.3s;
        &:hover {
          // background: rgba($blue, 0.3);
          background-color: #1f507b;
        }
      }
    }
    & > .os-scrollbar-horizontal {
      width: 100%;
      bottom: auto;
      top: 76px;
      border-radius: 8px;
      background-color: rgba($blue, 0.2);
      padding: 0;
      &:hover .os-scrollbar-track > .os-scrollbar-handle {
        background-color: $blue;
      }
      & > .os-scrollbar-track {
        & > .os-scrollbar-handle {
          border-radius: 8px;
          background-color: $blue;
          max-width: calc(50% - 15px);
          &.active {
            background-color: $blue;
          }
        }
      }
    }
    & > .os-scrollbar-vertical {
      display: none;
    }
    &:-webkit-full-screen {
      .game-table__col {
        padding-bottom: 50px;
      }

      .standings {
        &__top-labels {
          // position: fixed;
          // top: 0;
          // z-index: 2;
          padding-left: 30px;
        }
        &__game-table {
          padding-left: 30px;
          // padding-top: 85px;
        }
      }
      & > .os-scrollbar-horizontal {
        top: auto;
        bottom: 0;
      }
      .fullscreen-off-btn {
        display: flex;
      }
    }
    &:-moz-full-screen {
      .game-table__col {
        padding-bottom: 50px;
      }
      .standings {
        &__top-labels {
          // position: fixed;
          // top: 0;
          // z-index: 2;
          padding-left: 30px;
        }
        &__game-table {
          padding-left: 30px;
          // padding-top: 85px;
        }
      }
      & > .os-scrollbar-horizontal {
        top: auto;
        bottom: 0;
      }
      .fullscreen-off-btn {
        display: flex;
      }
    }
    &:-ms-fullscreen {
      .game-table__col {
        padding-bottom: 50px;
      }
      .standings {
        &__top-labels {
          // position: fixed;
          // top: 0;
          // z-index: 2;
          padding-left: 30px;
        }
        &__game-table {
          padding-left: 30px;
          // padding-top: 85px;
        }
      }
      & > .os-scrollbar-horizontal {
        top: auto;
        bottom: 0;
      }
      .fullscreen-off-btn {
        display: flex;
      }
    }
    &:fullscreen {
      .game-table__col {
        padding-bottom: 50px;
      }
      .standings {
        &__top-labels {
          // position: fixed;
          // top: 0;
          // z-index: 2;
          padding-left: 30px;
        }
        &__game-table {
          padding-left: 30px;
          // padding-top: 85px;
        }
      }
      & > .os-scrollbar-horizontal {
        top: auto;
        bottom: 0;
      }
      .fullscreen-off-btn {
        display: flex;
      }
    }
  }
}

.icon {
  &-circle {
    color: #28ffbf;
  }
  &-cancel {
    color: #ff5d9e;
  }
  &-cute-skull {
    color: #8f71ff;
  }
}

.game-table {
  display: inline-flex;
  align-items: stretch;
  background-color: #2d2358;
  padding-left: 8px;
  &:-webkit-full-screen {
    padding-left: 30px;
  }
  &:-moz-full-screen {
    padding-left: 30px;
  }
  &:-ms-fullscreen {
    padding-left: 30px;
  }
  &:fullscreen {
    padding-left: 30px;
  }
  &.compact-view {
    .game-table {
      &__col {
        display: block;
        &-item_with-3-place {
          margin-top: 0;
        }
      }
      &__line-wrap {
        display: none;
      }
    }
  }
  &__col {
    display: grid;
    grid-auto-flow: row;
    align-items: center;
    min-width: 225px;
    width: 225px;
    padding-top: 12px;
    padding-bottom: 12px;
    position: relative;
    &.highlighted {
      &::before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        left: -8px;
        width: calc(100% - 14px);
        height: 100%;
        background-color: #382e61;
      }
    }
    &_with-3-place {
      justify-content: center;
      align-items: initial;
      flex-direction: column;
      display: flex;
    }
    &-item {
      display: grid;
      grid-template-columns: 1fr 30px;
      align-items: center;
      align-self: stretch;
      position: relative;
      &_with-3-place {
        margin-top: 124px;
      }
    }
  }
  &__game {
    width: 100%;
    height: 100%;
    display: grid;
    position: relative;
    grid-template-columns: 1fr 23px;
    background-color: $white;
    border-radius: 8px;
    overflow: hidden;
    &-wrap {
      width: 100%;
      height: 124px;
      padding-top: 12px;
      padding-bottom: 12px;
    }
    &-result {
      justify-content: center;
      align-items: center;
      display: flex;
      grid-row: 1 / 3;
      grid-column: 2;
      //   grid-column-start: 2;
      //   grid-column-end: auto;
      height: 100%;
      background-color: $blue;
      color: $black;
    }
  }
  &__paricipant {
    width: 100%;
    height: 100%;
  }
  &__line {
    width: 44%;
    height: 50%;
    position: absolute;
    bottom: 0;
    left: 0;
    opacity: 0.4;
    border-top-right-radius: 4px;
    border-top: 2px solid $white;
    border-right: 2px solid $white;
    &::after {
      content: '';
      display: block;
      position: absolute;
    }
    &-wrap {
      width: 30px;
      height: 100%;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      &_even {
        &::before {
          content: '';
          display: block;
          position: absolute;
          top: -2px;
          right: 0;
          opacity: 0.4;
          width: 56%;
          height: 2px;
          background-color: $white;
          z-index: 1;
        }
        .game-table__line {
          bottom: auto;
          top: 0;
          border-top-right-radius: initial;
          border-bottom-right-radius: 4px;
          border-top: none;
          border-bottom: 2px solid $white;
        }
      }
    }
  }
}

.participant {
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
  grid-column: 1;
  padding-right: 10px;
  padding-left: 10px;
  &__link {
    flex-grow: 1;
    display: flex;
    align-items: center;
  }
  &__icon {
    width: 24px;
    height: 24px;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 6px;
    flex-shrink: 0;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__name {
    font-size: 0.8em;
    line-height: 2.083;
    color: $black;
    letter-spacing: -0.24px;
    @include text-limit(1);
  }
  &__result-wrap {
    display: flex;
    align-items: center;
    margin-left: 8px;
  }
  &__result-icon {
    margin-right: 11px;
    display: flex;
    align-items: center;
  }
  &__score {
    font-style: 1em;
    line-height: 1.667;
    color: $black;
    font-weight: 800;
    letter-spacing: -0.24px;
  }
}

// media
@include w(1440) {
  .tournament-desc-wrap {
    padding-right: 450px;
  }
}

@include w($md) {
  .tournament {
    &-short-info {
      &__wrapper {
        position: static;
        width: auto;
        height: auto;
        margin-bottom: 32px;
      }
      &__block {
        position: static;
        padding: 24px 16px;
      }
      &__row {
        grid-template-columns: 1fr 1fr;
        grid-gap: 32px 50px;
        &:nth-child(2) {
          margin-top: 32px;
        }
        &_full {
          grid-template-columns: 1fr;
          &:last-child {
            grid-template-columns: 1fr 1fr;
          }
        }
      }
      &__col {
        &_title {
          max-width: none;
        }
      }
    }
    &-desc-wrap {
      padding-right: 0;
    }
    &-desc-col {
      max-width: none;
    }
    &__top-img {
      margin-bottom: 16px;
    }
    &-about {
      &__table {
        grid-template-columns: 1fr 1fr;
        grid-gap: 24px 20px;
      }
    }
    &__info-section {
      padding-top: 70px;
    }
    &__maps-pool {
      grid-template-columns: repeat(4, 1fr);
      grid-gap: 24px 20px;
    }
  }
  .standings {
    padding-top: 70px;
    &__header-top {
      flex-wrap: wrap;
      .title-h2 {
        width: 100%;
        padding-bottom: 24px;
      }
    }
    &__status-item {
      margin-bottom: 24px;
      &:nth-child(2) {
        margin-left: 0;
      }
      &:nth-child(4) {
        margin-right: 24px;
      }
    }
    &__controls-item {
      margin-bottom: 24px;
    }
    &__body {
      margin-top: 20px;
    }
    // &__top-labels {
    //   padding-top: 20px;
    // }
  }
}

@include w($sm) {
  .tournament {
    &-short-info {
      &__block {
        padding: 20px 16px 16px;
      }
      &__row {
        grid-gap: 16px 30px;
        &:nth-child(2) {
          margin-top: 16px;
        }
        &_full {
          &:last-child {
            grid-template-columns: 1fr;
          }
        }
      }
    }
    &-desc-wrap {
      margin-top: 24px;
    }
    &-about {
      &__title-h3 {
        margin-top: 30px;
      }
    }
    &__info-section {
      padding-top: 40px;
      .title-h2 {
        margin-bottom: 24px;
      }
    }
    &__maps-pool {
      grid-template-columns: repeat(2, 1fr);
    }
    &__all-teams-btn {
      margin-top: 16px;
      width: 100%;
    }
  }
  .grid-col {
    &_title {
      padding-bottom: 16px;
    }
    &_full {
      &:last-child {
        grid-column: 1/3;
      }
    }
  }
  .team-item {
    display: block;
    &__profile {
      padding-left: 65px;
    }
  }
  .standings {
    &__status-item {
      margin-bottom: 16px;
      margin-right: 14px;
      &:nth-child(4) {
        margin-right: 14px;
      }
    }
    &__controls-item {
      margin-bottom: 16px;
      margin-right: 16px;
    }
  }
}
