.top-articles {
  padding-bottom: 40px;
  .title-h2 {
    margin-bottom: 45px;
  }
  &__block {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px;
    .article-preview {
      &__img {
        a {
          padding-top: 46.4%;
        }
      }
      &__caption {
        font-family: 'SF Pro Display';
        margin-top: 12px;
        font-size: 1.67em;
        line-height: 1.4;
        letter-spacing: 0.35px;
      }
    }
  }
}

.article-preview {
  display: flex;
  flex-direction: column;
  &__img {
    border-radius: 12px;
    overflow: hidden;
    a {
      display: block;
      width: 100%;
      height: 0;
      position: relative;
      padding-top: 56.2%;
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__tags {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: 20px;
  }
  &__action {
    margin-left: 50px;
  }
  &__caption {
    flex-grow: 1;
    margin-top: 5px;
    font-size: 1em;
    line-height: 1.6;
    color: $white;
    font-weight: 800;
    letter-spacing: -0.24px;
  }
  &__info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 12px;
    font-size: 0.67em;
    line-height: 2.4;
    color: $white;
    letter-spacing: 0.12px;
  }
  &__date {
    margin-right: 50px;
  }
  &__reading {
    flex-shrink: 0;
    margin-left: auto;
  }
  &__author {
    display: flex;
    align-items: center;
    margin-right: 50px;
    &-img {
      flex-shrink: 0;
      width: 32px;
      height: 32px;
      border-radius: 50%;
      overflow: hidden;
    }
    &-name {
      margin-left: 12px;
      line-height: 1.6;
      transition: $anim;
      text-decoration: underline rgba($white, 0);
      @include offHoverOnDevices {
        &:hover {
          color: $blue;
          text-decoration-color: rgba($blue, 1);
        }
      }
    }
  }
  &__text {
    @include text-white15;
  }
}

.latest-news {
  padding-top: 45px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 30px;
  .article-preview {
    &__info {
      margin-top: 16px;
    }
  }
}

.event-banner {
  padding-top: 40px;
  padding-bottom: 80px;
  &__content {
    position: relative;
    padding: 60px 77px;
    .button {
      margin-top: 18px;
    }
  }
  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 20px;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__desc {
    max-width: 360px;
    position: relative;
  }
  &__text {
    margin-top: 20px;
    @include text-white15;
  }
}

.popular-articles {
  background-color: $violet1;
  padding: 80px 0;
  .title-h2 {
    margin-bottom: 45px;
  }
  &__wrapper {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 45px 30px;
  }
}

.news-feed {
  padding-top: 80px;
  .title-h2 {
    margin-bottom: 45px;
  }
  &__wrapper {
    display: grid;
    grid-template-columns: 1fr;
    grid-row-gap: 45px;
    .subscribe-banner {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 78px;
      min-height: 240px;
      &__desc {
        flex-basis: 360px;
      }
      &__caption {
        display: flex;
        align-items: center;
        span {
          margin-left: 28px;
        }
      }
      &__text {
        margin-top: 15px;
      }
      &__form {
        flex-basis: 50%;
      }
    }
  }
  .article-preview {
    display: grid;
    grid-template-columns: 51.3% 1fr auto;
    &__img {
      margin-right: 30px;
      grid-row: 1/6;
      a {
        padding-top: 48.8%;
      }
    }
    &__tags {
      margin-top: 0;
      grid-column: 2/4;
    }
    &__caption {
      font-family: 'SF Pro Display';
      margin-top: 12px;
      grid-column: 2/4;
      font-size: 1.67em;
      line-height: 1.4;
      letter-spacing: 0.35px;
    }
    &__info {
      grid-column: 2/4;
      grid-row: 5;
    }
    &__text {
      margin-top: 16px;
      grid-column: 2/4;
    }
  }
  .load-more {
    margin-top: 50px;
    display: flex;
    justify-content: center;
    .button {
      padding-left: 25px;
      padding-right: 25px;
      border: 1px solid $blue;
    }
  }
}

.subscribe-banner {
  border-radius: 20px;
  overflow: hidden;
  background-color: $violet1;
  &__text {
    @include text-white15;
  }
  &__form {
    .subscribe-form {
      max-width: none;
      padding-top: 0;
      &__email-label {
        margin-bottom: 8px;
      }
      &__banner-field {
        display: flex;
        align-items: center;
      }
      &__email-input {
        flex-grow: 1;
        width: 100%;
        padding: 5px 12px;
        border-radius: 8px;
        background-color: $white;
        border: 1px solid $border;
        @include text-white15;
        color: $violet2;
        &::placeholder {
          opacity: 0.2;
        }
      }
      &__submit {
        width: auto;
        padding-left: 20px;
        padding-right: 20px;
        margin-top: 0;
        margin-left: 16px;
      }
      &__agree {
        margin-top: 16px;
      }
    }
  }
}

// media
@include w($md) {
  .top-articles {
    padding-bottom: 35px;
    .title-h2 {
      margin-bottom: 30px;
    }
    &__block {
      grid-column-gap: 20px;
      .article-preview {
        &__caption {
          font-size: 1.34em;
          line-height: 1.2;
        }
      }
    }
  }
  .latest-news {
    padding-top: 30px;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 20px;
    .article-preview {
      &__info {
        margin-top: 12px;
      }
    }
  }
  .article-preview {
    &__caption {
      margin-top: 12px;
    }
  }
  .event-banner {
    padding-top: 35px;
    padding-bottom: 70px;
    &__content {
      padding: 30px 32px;
      .button {
        margin-top: 20px;
      }
    }
    &__text {
      margin-top: 16px;
    }
  }
  .popular-articles {
    padding: 70px 0;
    .title-h2 {
      margin-bottom: 30px;
    }
    &__wrapper {
      grid-template-columns: repeat(2, 1fr);
      grid-gap: 30px 20px;
    }
    .article-preview {
      &__caption {
        margin-top: 8px;
      }
    }
  }
  .news-feed {
    padding-top: 70px;
    .title-h2 {
      margin-bottom: 30px;
    }
    &__wrapper {
      grid-template-columns: repeat(2, 1fr);
      grid-gap: 30px 20px;
      .subscribe-banner {
        grid-column: 1/3;
        padding: 55px 32px;
      }
    }
    .article-preview {
      display: flex;
      &__img {
        margin-right: 0;
        a {
          padding-top: 55.7%;
        }
      }
      &__tags {
        margin-top: 20px;
      }
      &__caption {
        font-family: 'SF Pro Text';
        font-size: 1em;
        line-height: 1.6;
        letter-spacing: -0.24px;
      }
      &__text {
        margin-top: 12px;
      }
      &__info {
        margin-top: 24px;
      }
    }
    .load-more {
      margin-top: 35px;
    }
  }
}

@include w(992) {
  .news-feed {
    &__wrapper {
      .subscribe-banner {
        display: block;
      }
    }
  }
  .subscribe-banner {
    &__desc {
      max-width: 465px;
    }
    &__form {
      margin-top: 32px;
      .subscribe-form {
        &__email-input {
          max-width: 320px;
        }
        &__submit {
          margin-left: 12px;
        }
      }
    }
  }
}

@include w($sm) {
  .top-articles {
    padding-bottom: 20px;
    .title-h2 {
      margin-bottom: 20px;
    }
    &__block {
      grid-template-columns: 1fr;
      .article-preview {
        &__caption {
          margin-top: 8px;
          line-height: 1.5;
        }
      }
    }
  }
  .latest-news {
    grid-template-columns: 1fr;
    .article-preview {
      &__caption {
        font-size: 1.34em;
        line-height: 1.5;
        letter-spacing: 0.35px;
      }
      &__info {
        margin-top: 16px;
      }
    }
  }
  .article-preview {
    &__caption {
      margin-top: 8px;
    }
    &__info {
      margin-top: 16px;
    }
  }
  .event-banner {
    padding-top: 55px;
    padding-bottom: 45px;
    &__content {
      padding: 32px 16px;
      .button {
        width: 100%;
        margin-top: 16px;
      }
    }
    &__text {
      margin-top: 22px;
    }
  }
  .popular-articles {
    padding: 45px 0;
    &__wrapper {
      grid-template-columns: 1fr;
    }
    .article-preview {
      &__caption {
        margin-top: 8px;
      }
      &__info {
        margin-top: 10px;
      }
    }
  }
  .news-feed {
    padding-top: 45px;
    &__wrapper {
      grid-template-columns: 1fr;
      .subscribe-banner {
        grid-column: auto;
        padding: 45px 16px;
        &__caption {
          span {
            margin-left: 16px;
          }
        }
      }
    }
    .article-preview {
      &__caption {
        margin-top: 8px;
      }
      &__text {
        margin-top: 8px;
      }
      &__info {
        margin-top: 10px;
      }
    }
  }
  .subscribe-banner {
    &__form {
      margin-top: 20px;
      .subscribe-form {
        &__banner-field {
          flex-wrap: wrap;
        }
        &__email-input {
          max-width: none;
        }
        &__submit {
          width: 100%;
          margin-left: 0;
          margin-top: 16px;
        }
        &__agree {
          label {
            line-height: 14px;
          }
        }
      }
    }
    .icon-maingame {
      font-size: 28px;
    }
  }
}
